import { TInputAnswer } from 'components/question-forms';
import RadioBtn from 'components/shared/radio-btn/radio-btn';
import { useEffect, useRef } from 'react';
import styles from './question-list.module.scss';

export type TQuestionButton = {
  label: string;
  id: number;
};

export type TUserAnswer = {
  answer: string | Array<TInputAnswer>;
  id: number;
  title: string;
  answerId?: string | number;
};

export interface IQuestionList {
  title: string;
  list: Array<TQuestionButton>;
  userAnswers: Array<TUserAnswer> | null;
  currentQuestionId: number;
  onClick: (ind: number) => void;
}

const QuestionList = ({
  title,
  list,
  userAnswers,
  currentQuestionId,
  onClick
}: IQuestionList) => {
  const questionListRef = useRef<HTMLUListElement | null>(null);

  useEffect(() => {
    if (questionListRef.current) {
      questionListRef.current.scrollLeft = questionListRef.current.scrollWidth;
    }
  }, [userAnswers]);

  return (
    <>
      <h4 className={styles.title}>{title}</h4>
      <ul className={styles.questionList} ref={questionListRef}>
        {list.map(({ label, id }, ind) => {
          const isChecked = userAnswers?.find(
            (el) => el.id === id && el.answer
          );
          const isCurrentQuestion = currentQuestionId === id;
          return (
            <li key={label + id}>
              <RadioBtn
                variant="small"
                label={label}
                checked={!!isChecked}
                isCurrent={isCurrentQuestion}
                onClick={() => onClick(ind)}
              />
            </li>
          );
        })}
      </ul>
    </>
  );
};

export default QuestionList;
