import React, { useEffect, useState } from 'react';
import { TQuestionFormProps } from '.';
import { getErrorMsg, getFieldStatus } from 'utils/form';
import {
  Select,
  SelectChangeEvent,
  Grid,
  MenuItem,
  FormControl,
  InputLabel,
  Box
} from '@mui/material';
import { NATURE_OF_ACTION } from 'constants/form';
import TextField from 'components/shared/text-field/text-field';
import styles from './forms.module.scss';
import FormHelperText from '@mui/material/FormHelperText';
import Placeholder from 'components/shared/placeholder/placeholder';
import Title from 'components/shared/title/title';
import Subtitle from 'components/shared/subtitle/subtitle';

const catetoryArr = Object.keys(NATURE_OF_ACTION);

const NatureOfActionForm = ({
  handleFormAnswers,
  userFormsAnswers,
  errors
}: TQuestionFormProps) => {
  const fieldState = getFieldStatus(userFormsAnswers, 'natureOfAction');
  const additionalJudgDetailsField = getFieldStatus(
    userFormsAnswers,
    'addJudgDetailsField'
  );
  const asnwerFromCache = fieldState?.value?.split(', ');
  const [category, setCategory] = useState(asnwerFromCache?.[0] || '');
  const [categoryAnswer, setCategoryAnswer] = useState<string>(
    asnwerFromCache?.[1] || ''
  );
  const [addJudgDetails, setAddJudgDetails] = useState(
    additionalJudgDetailsField?.value || ''
  );

  useEffect(() => {
    const natureOfSuitValue = category ? `${category}, ${categoryAnswer}` : '';
    handleFormAnswers('natureOfAction', natureOfSuitValue);
    handleFormAnswers('addJudgDetailsField', addJudgDetails);
  }, [category, categoryAnswer, addJudgDetails]);

  const handleChangeCategory = (event: SelectChangeEvent) => {
    const value = event.target.value as string;
    setCategory(value);
    setCategoryAnswer(NATURE_OF_ACTION[value]);
  };

  const isOtherCategory = category === 'Other';

  return (
    <>
      <Grid container spacing="24px" mb={2}>
        <Grid item xs={12}>
          <FormControl
            fullWidth
            error={!!getErrorMsg(errors, 'natureOfAction')}
          >
            <InputLabel id="suit-type" shrink>
              Nature of Action
            </InputLabel>
            <Select
              fullWidth
              value={category}
              onChange={handleChangeCategory}
              label="Nature of Action"
              labelId="suit-type"
              displayEmpty
              notched
              renderValue={(selected) =>
                !selected.length ? (
                  <Placeholder text="Nature of Action" />
                ) : (
                  selected
                )
              }
            >
              {catetoryArr.map((category) => {
                return (
                  <MenuItem value={category} key={category}>
                    {category}
                  </MenuItem>
                );
              })}
            </Select>
            {!!getErrorMsg(errors, 'natureOfAction') && (
              <FormHelperText>
                {getErrorMsg(errors, 'natureOfAction')}
              </FormHelperText>
            )}
          </FormControl>
          {category && (
            <Box pt="10px">
              {!isOtherCategory ? (
                <>
                  <div className={styles.categoryAnswer}>{categoryAnswer}</div>
                </>
              ) : (
                <TextField
                  placeholder="Enter nature of action"
                  value={categoryAnswer}
                  onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                    setCategoryAnswer(event.target.value);
                  }}
                />
              )}
            </Box>
          )}
        </Grid>
        <Grid item xs={12}>
          <Title text="Additional Judgment Details" variant="medium" />
          <Subtitle
            text="Anything else you would like us to know?"
            variant="small"
          />
          <TextField
            placeholder="I am a time traveler"
            value={addJudgDetails}
            onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
              setAddJudgDetails(event.target.value);
            }}
          />
        </Grid>
      </Grid>
    </>
  );
};

export default NatureOfActionForm;
