import { useEffect, useState } from 'react';
import { TQuestionFormProps } from '.';
import { getErrorMsg, getFieldStatus, getFormatDate } from 'utils/form';
import dayjs, { Dayjs } from 'dayjs';
import { TextField as MUITextField } from '@mui/material';
import { DesktopDatePicker } from '@mui/x-date-pickers/DesktopDatePicker';

const CourtFilingDateForm = ({
  handleFormAnswers,
  errors,
  userFormsAnswers
}: TQuestionFormProps) => {
  const fieldState = getFieldStatus(userFormsAnswers, 'courtFilingDate');
  const [date, setDate] = useState<Dayjs | null>(
    dayjs(fieldState?.value || '')
  );
  const [fieldValue, setValue] = useState<string | undefined>(
    fieldState?.value || ''
  );

  useEffect(() => {
    handleFormAnswers('courtFilingDate', fieldValue);
  }, []);

  const handleChange = (
    newValue: Dayjs | null,
    keyboardInputValue: string | undefined
  ) => {
    if (newValue) {
      setDate(newValue);
      const formatedValue = newValue.isValid()
        ? getFormatDate(newValue)
        : keyboardInputValue;
      setValue(formatedValue);
      handleFormAnswers('courtFilingDate', formatedValue);
      return;
    }
  };

  return (
    <>
      <DesktopDatePicker
        inputFormat="MM/DD/YYYY"
        value={date}
        onChange={handleChange}
        disableFuture
        renderInput={(params) => (
          <MUITextField
            {...params}
            error={!!getErrorMsg(errors, 'courtFilingDate')}
            className="datePickerError"
            fullWidth
            helperText={getErrorMsg(errors, 'courtFilingDate')}
          />
        )}
      />
    </>
  );
};

export default CourtFilingDateForm;
