import { useEffect, useState } from 'react';
import TextField from 'components/shared/text-field/text-field';
import { TQuestionFormProps } from '.';
import { getErrorMsg, getFieldStatus } from 'utils/form';

const BusinessTitle = ({
  handleFormAnswers,
  errors,
  isBusinessCreditorType,
  userFormsAnswers
}: TQuestionFormProps) => {
  const fieldState = getFieldStatus(userFormsAnswers, 'businessTitle');
  const [fieldValue, setValue] = useState(fieldState?.value || '');

  useEffect(() => {
    handleFormAnswers('businessTitle', fieldValue);
  }, [fieldValue]);

  return (
    <TextField
      placeholder={
        isBusinessCreditorType
          ? 'CEO, CFO, Project Manager, etc.'
          : 'Self, Attorney, Legal Guardian, Trustee, Spouse, etc.'
      }
      value={fieldValue}
      errorMsg={getErrorMsg(errors, 'businessTitle')}
      onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
        const value = event.target.value;
        setValue(value);
      }}
      InputLabelProps={{ shrink: true }}
    />
  );
};

export default BusinessTitle;
