import { useEffect, useState } from 'react';
import TextField from 'components/shared/text-field/text-field';
import { TQuestionFormProps } from '.';
import { getErrorMsg, getFieldStatus } from 'utils/form';

const ContactNameForm = ({
  handleFormAnswers,
  userFormsAnswers,
  errors
}: TQuestionFormProps) => {
  const fieldState = getFieldStatus(userFormsAnswers, 'contactName');
  const [fieldValue, setValue] = useState(fieldState?.value || '');

  useEffect(() => {
    handleFormAnswers('contactName', fieldValue);
  }, []);

  return (
    <TextField
      placeholder="Biff Tannen"
      value={fieldValue}
      errorMsg={getErrorMsg(errors, 'contactName')}
      onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
        const value = event.target.value;
        setValue(value);
        handleFormAnswers('contactName', value);
      }}
    />
  );
};

export default ContactNameForm;
