import { useEffect, useState } from 'react';
import TextField from 'components/shared/text-field/text-field';
import { TQuestionFormProps } from '.';
import { getErrorMsg, getFieldStatus } from 'utils/form';

const JudgmentReasonForm = ({
  handleFormAnswers,
  errors,
  userFormsAnswers
}: TQuestionFormProps) => {
  const fieldState = getFieldStatus(userFormsAnswers, 'judgmentReason');
  const [fieldValue, setValue] = useState(fieldState?.value || '');

  useEffect(() => {
    handleFormAnswers('judgmentReason', fieldValue);
  }, []);

  return (
    <TextField
      placeholder="Enter reason for seeking judgment"
      value={fieldValue}
      errorMsg={getErrorMsg(errors, 'judgmentReason')}
      onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
        const value = event.target.value;
        setValue(value);
        handleFormAnswers('judgmentReason', value);
      }}
    />
  );
};

export default JudgmentReasonForm;
