import { ONE_ROW_FIELDS_REGEX } from 'constants/regex';

export const getFieldStatus = (
  userFormsAnswers: Array<TInputAnswer> | null,
  fieldName: TFieldName
) => userFormsAnswers?.find((el) => el.fieldName === fieldName);

export const getErrorMsg = (
  errors: Array<Record<string, string>> | null,
  fieldName: TFieldName
) => errors && errors.find((error) => error.fieldName === fieldName)?.errorMsg;

export const getMapApiLink = (zipcode: string) =>
  `${GOGLE_MAPS_API}?address=${zipcode}&key=${GOOGLE_API_KEY}`;

export const getCityState = (
  mapResponse: google.maps.GeocoderResponse | never
) => {
  const address_components = mapResponse.results[0].address_components;
  const city = address_components.find(
    (adr: { types: string[] }) => adr.types[0] === 'locality'
  );
  const state = address_components.find(
    (adr: { types: string[] }) => adr.types[0] === 'administrative_area_level_1'
  );

  return [`${city ? city.long_name : ''}`, `${state ? state.short_name : ''}`];
};

export const maskDate = (value: string) => {
  if (value.length > 4) {
    return (value = `${value.slice(0, 2)}/${value.slice(2, 4)}/${value.slice(
      4
    )}`);
  }
  if (value.length > 2) {
    return (value = `${value.slice(0, 2)}/${value.slice(2)}`);
  }
  return value;
};

export const maskPhoneNumber = (value: string) => {
  if (value.length > 6) {
    return (value = `(${value.slice(0, 3)}) ${value.slice(3, 6)}-${value.slice(
      6
    )}`);
  }
  if (value.length > 3) {
    return (value = `(${value.slice(0, 3)}) ${value.slice(3)}`);
  }
  return value;
};

export const maskSsnAndEinNumber = (isBusinessType: boolean, value: string) => {
  if (isBusinessType) {
    if (value.length > 2) {
      return (value = `${value.slice(0, 2)}-${value.slice(2)}`);
    }
  }
  if (!isBusinessType) {
    if (value.length > 5) {
      return (value = `${value.slice(0, 3)}-${value.slice(3, 5)}-${value.slice(
        5
      )}`);
    }
    if (value.length > 3) {
      return (value = `${value.slice(0, 3)}-${value.slice(3)}`);
    }
  }
  return value;
};

export const mapAutoCompleteAddress = (
  places: google.maps.places.PlaceResult
) => {
  const city = places.address_components?.find(
    (adr: { types: string[] }) => adr.types[0] === 'locality'
  );
  const state = places.address_components?.find(
    (adr: { types: string[] }) => adr.types[0] === 'administrative_area_level_1'
  );
  const zipCode = places.address_components?.find(
    (adr: { types: string[] }) => adr.types[0] === 'postal_code'
  );
  const street = places.address_components?.find(
    (adr: { types: string[] }) => adr.types[0] === 'route'
  );
  const streetNumber = places.address_components?.find(
    (adr: { types: string[] }) => adr.types[0] === 'street_number'
  );

  return [
    `${zipCode ? zipCode.long_name : ''}`,
    `${city ? city.long_name : ''}`,
    `${state ? state.short_name : ''}`,
    `${street ? street.long_name : ''}`,
    `${streetNumber ? streetNumber.long_name : ''}`
  ];
};

export const getGoogleStreet = (places: google.maps.places.PlaceResult) => {
  return places.address_components?.find(
    (adr: { types: string[] }) => adr.types[0] === 'route'
  );
};

export const getFormatDate = (value: Dayjs) => {
  const month = value.get('M') + 1;
  const day = value.get('D');
  const year = value.get('year').toString();
  return `${month < 10 ? `0${month}` : month}/${
    day < 10 ? `0${day}` : day
  }/${year}`;
};

export const getDeptors = (userAnswers: Array<TUserAnswer>) => {
  const filteredDebtors = userAnswers?.filter(
    (asnwer) =>
      asnwer.answerId === 'individual' || asnwer.answerId === 'business'
  );
  const debtors = filteredDebtors
    ? filteredDebtors.map((deptAnsw) => deptAnsw.answerId)
    : ['individual'];

  return debtors as string[];
};

export const getAge = (dateString: string) => {
  const today = new Date();
  const birthDate = new Date(dateString);
  let age = today.getFullYear() - birthDate.getFullYear();
  const m = today.getMonth() - birthDate.getMonth();
  if (m < 0 || (m === 0 && today.getDate() < birthDate.getDate())) {
    age--;
  }
  return age;
};

export const itemsToString = (arr: string[]) =>
  arr.reduce((prev, cur) => {
    if (cur) {
      prev += `${cur} `;
    }
    return prev;
  }, '');

