export const sortOrder = Object.keys(getFieldLabel);

export const mapUserAnswersToReviewModel = (
  questions: TQuestionsList,
  userFormAnswsers: Array<TInputAnswer> | null,
  userOptionAnsers: Array<TUserAnswer> | null,
  allPostJudgments: TDebtorsPostJudgmentFormProps['allPostJudgPayments'],
  files: Array<File> | null,
  isBusinessCreditorType: boolean
) => {
  if (!userFormAnswsers || !userOptionAnsers) return;
  const questionBeforeReview = [...questions.slice(0, questions.length - 2)];

  return questionBeforeReview
    .map(({ title, id, formId }) => {
      const optionAnswer = userOptionAnsers.find((el) => el.title === title);
      let fieldsAnswers = userFormAnswsers.filter(
        (el) =>
          el.formId === formId &&
          el.value &&
          !ONE_ROW_FIELDS_REGEX.test(el.fieldName)
      );
      const credInfoBisRegex =
        /businessCreditorName|businessTitle|bisEmail|bisAddress|bisPhone|bisAltPhone|creditorContactBisFullName/;
      if (formId === 'creditorInfo') {
        fieldsAnswers = fieldsAnswers.filter((el) =>
          isBusinessCreditorType
            ? credInfoBisRegex.test(el.fieldName)
            : !credInfoBisRegex.test(el.fieldName)
        );
      }

      const postJudgments = allPostJudgments?.filter(
        (el) => el.formId === formId && (el.amount || el.date)
      );

      const fields =
        formId === 'uploadDocuments'
          ? files?.map((file) => ({
              fieldName: '',
              value: file.name,
              questionId: id
            }))
          : fieldsAnswers;

      fields?.sort((a, b) => {
        const debRgx = /individual\d|business\d/g;
        const isDebtorFields = debRgx.test(a.fieldName);
        if (isDebtorFields) {
          const newA = a.fieldName.replace(debRgx, '');
          const newB = b.fieldName.replace(debRgx, '');
          return sortOrder.indexOf(newA) - sortOrder.indexOf(newB);
        }
        return sortOrder.indexOf(a.fieldName) - sortOrder.indexOf(b.fieldName);
      });

      const titles: Record<string, string> = {
        creditorInfo: 'Creditor Information',
        debtorInformation: 'Debtor Information',
        coDeptor1Information: 'Co-Debtor 1 Information',
        coDeptor2Information: 'Co-Debtor 2 Information',
        coDeptor3Information: 'Co-Debtor 3 Information',
        coDeptor4Information: 'Co-Debtor 4 Information',
        uploadDocuments: 'Upload Documents'
      };

      return {
        isForm: !!formId,
        questionTitle: titles[formId || ''] || title || '',
        userAnswers: formId ? fields : [optionAnswer?.answer],
        postJudgments: postJudgments,
        formId
      };
    })
    .filter((el) => el.userAnswers?.length);
};
