import Button from 'components/shared/button/button';
import Modal, { TModalProps } from '../modal';
import ModalTitle from '../modal-title';
import ModalActions from '../modal-actions';
import { redirectToMainPage } from 'utils/general';

const DebtorPaymentsModal = ({ isOpen, onClose }: TModalProps) => {
  return (
    <Modal isOpen={isOpen} onClose={onClose}>
      <>
        <ModalTitle
          title={`Unfortunately, we are unable to enforce this judgment as we can only accept judgments that are currently valid and outstanding.`}
        />
        <ModalActions
          actions={[
            <Button
              label="Go to the main page"
              onClick={() => {
                redirectToMainPage();
              }}
            />,
            <Button label="Back" variant="secondary" onClick={onClose} />
          ]}
        />
      </>
    </Modal>
  );
};

export default DebtorPaymentsModal;
