import { useEffect, useState } from 'react';
import TextField from 'components/shared/text-field/text-field';
import { TQuestionFormProps } from '.';
import { getErrorMsg, getFieldStatus } from 'utils/form';

const EmailForm = ({
  handleFormAnswers,
  errors,
  userFormsAnswers,
  fieldName
}: TQuestionFormProps) => {
  const fieldNameValue = fieldName || '';
  const fieldState = getFieldStatus(userFormsAnswers, fieldNameValue);
  const [fieldValue, setValue] = useState(fieldState?.value || '');

  useEffect(() => {
    handleFormAnswers(fieldNameValue, fieldValue);
  }, [fieldValue]);

  return (
    <TextField
      placeholder="martymcfly@gmail.com"
      value={fieldValue}
      type="email"
      errorMsg={getErrorMsg(errors, fieldNameValue)}
      onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
        const value = event.target.value;
        setValue(value);
      }}
    />
  );
};

export default EmailForm;
