import { useEffect, useState } from 'react';
import { TQuestionFormProps } from '.';
import { getFieldStatus } from 'utils/form';

const AdditionalDetailsForm = ({
  handleFormAnswers,
  userFormsAnswers
}: TQuestionFormProps) => {
  const fieldState = getFieldStatus(userFormsAnswers, 'additionalDetails');
  const [fieldValue, setValue] = useState(fieldState?.value || '');

  useEffect(() => {
    handleFormAnswers('additionalDetails', fieldValue);
  }, []);

  return (
    <textarea
      style={{
        resize: 'vertical',
        minHeight: '120px'
      }}
      placeholder="Enter additional details"
      value={fieldValue}
      onChange={(event: React.ChangeEvent<HTMLTextAreaElement>) => {
        const value = event.target.value;
        setValue(value);
        handleFormAnswers('additionalDetails', value);
      }}
    ></textarea>
  );
};

export default AdditionalDetailsForm;
