import { TQuestionFormProps } from '.';
import { Box } from '@mui/material';
import formStyles from './forms.module.scss';
import FilingCountyForm from './filing-county-form';
import Title from 'components/shared/title/title';
import FilingCourtForm from './filing-court-form';
import IndexNumberForm from './index-number-form';
import CourtFilingDateForm from './court-filing-date-form';
import JudgmentAmountForm from './judgment-amount-form';
import { BOX_FIELD_MB } from 'constants/spacing';

const JudgmentInfoFrom = ({
  handleFormAnswers,
  errors,
  userFormsAnswers,
  isBusinessCreditorType
}: TQuestionFormProps) => {
  return (
    <>
      <Box marginBottom={BOX_FIELD_MB}>
        <Box className={formStyles.titleFieldWithLabel}>
          <Title
            required
            variant="medium"
            text="Filing County"
            tooltipText="The filing county is the county where the judgment was entered. "
          />
        </Box>
        <FilingCountyForm
          errors={errors}
          handleFormAnswers={handleFormAnswers}
          userFormsAnswers={userFormsAnswers}
          isBusinessCreditorType={isBusinessCreditorType}
        />
      </Box>
      <Box marginBottom={BOX_FIELD_MB}>
        <Box className={formStyles.titleFieldWithLabel}>
          <Title
            required
            variant="medium"
            text="Filing Court"
            tooltipText="The filing court is the court that issued the judgment."
          />
        </Box>
        <FilingCourtForm
          errors={errors}
          handleFormAnswers={handleFormAnswers}
          userFormsAnswers={userFormsAnswers}
          isBusinessCreditorType={isBusinessCreditorType}
        />
      </Box>
      <Box marginBottom={BOX_FIELD_MB}>
        <Box className={formStyles.titleField}>
          <Title
            required
            variant="medium"
            text="Index Number"
            tooltipText="The index number is the number issued by the clerk or court to identify the case, judgment, or transcript.
"
          />
        </Box>
        <IndexNumberForm
          errors={errors}
          handleFormAnswers={handleFormAnswers}
          userFormsAnswers={userFormsAnswers}
          isBusinessCreditorType={isBusinessCreditorType}
        />
      </Box>
      <Box marginBottom={BOX_FIELD_MB}>
        <Box className={formStyles.titleField}>
          <Title
            required
            variant="medium"
            text="Award Date"
            tooltipText="The date the judgment was entered by the awarding court."
          />
        </Box>
        <CourtFilingDateForm
          errors={errors}
          handleFormAnswers={handleFormAnswers}
          userFormsAnswers={userFormsAnswers}
          isBusinessCreditorType={isBusinessCreditorType}
        />
      </Box>
      <Box marginBottom={{ xs: 0, md: BOX_FIELD_MB }}>
        <Box className={formStyles.titleField}>
          <Title
            required
            variant="medium"
            text="Award Amount"
            tooltipText="The amount awarded to the judgment creditor on the judgment award date."
          />
        </Box>
        <JudgmentAmountForm
          errors={errors}
          handleFormAnswers={handleFormAnswers}
          userFormsAnswers={userFormsAnswers}
          isBusinessCreditorType={isBusinessCreditorType}
        />
      </Box>
    </>
  );
};

export default JudgmentInfoFrom;
