import { createTheme } from '@mui/material/styles';

export const theme = createTheme({
  breakpoints: {
    values: {
      xs: 0,
      sm: 650,
      md: 900,
      lg: 1200,
      xl: 1536
    }
  },
  palette: {
    primary: {
      main: '#031C77'
    },
    secondary: {
      main: '#66666C'
    }
  },
  components: {
    MuiButton: {
      styleOverrides: {
        root: {
          fontFamily: 'Arimo'
        }
      }
    },
    MuiInputLabel: {
      // defaultProps: { shrink: true },
      styleOverrides: {
        root: {
          // make as var?
          background: '#fff',
          lineHeight: '1.2',
          color: '#66666C',
          // scaled to 12 by material transform styles
          // fontSize: '12px',
          fontWeight: 500,
          fontFamily: 'Arimo',
          // lineHeight: '12px'
          '&.Mui-error.Mui-focused': {
            color: '#031C77'
          },
          '&.Mui-focused': {
            color: '#031C77',
            fontSize: '1rem !important',
            fontWeight: '500 !important'
          }
        }
      }
    },
    MuiTextField: {
      defaultProps: {
        fullWidth: true
      },
      styleOverrides: {
        root: {
          input: {
            height: '52px',
            borderColor: 'transparent',
            borderRadius: 8
          }
        }
      }
    },
    MuiAutocomplete: {
      styleOverrides: {
        root: {
          '& .MuiOutlinedInput-root': {
            paddingBottom: 2,
            paddingTop: 2
          },
          input: {
            borderColor: 'transparent !important',
            borderRadius: 8
          }
        }
      }
    },
    MuiInputBase: {
      styleOverrides: {
        root: {
          fontFamily: 'Arimo',
          height: '52px',
          borderRadius: 8,
          '&:hover .MuiOutlinedInput-notchedOutline': {
            borderColor: '#DADDE5 !important'
          },
          '.MuiOutlinedInput-notchedOutline': {
            borderColor: '#DADDE5 !important',
            transition: '.3s border-color ease-out'
          },
          '&.Mui-error .MuiOutlinedInput-notchedOutline': {
            borderColor: '#D86464 !important'
          },
          '&.Mui-focused .MuiOutlinedInput-notchedOutline': {
            borderColor: '#031C77 !important',
            borderWidth: '1px !important'
          },
          '&.Mui-focused': {
            borderColor: '#031C77 !important',
            borderWidth: '1px !important'
          }
        }
      }
    },
    MuiSelect: {
      defaultProps: {
        SelectDisplayProps: {
          style: {
            padding: '12px 16px'
          }
        }
      }
    },
    MuiOutlinedInput: {
      styleOverrides: {
        root: {
          borderRadius: 8
        }
      }
    },
    MuiFormHelperText: {
      styleOverrides: {
        root: {
          '&.Mui-error': {
            color: '#D86464',
            textAlign: 'right',
            paddingTop: '2px',
            margin: '0',
            fontWeight: 600,
            fontSize: '12px',
            fontFamily: 'Arimo',
            letterSpacing: 'normal'
          }
        }
      }
    }
  }
});
