import { useEffect, useState } from 'react';
import { TDebtorsProps } from '.';
import { getErrorMsg, getFieldStatus } from 'utils/form';
import TextField from 'components/shared/text-field/text-field';
import { ONLY_DIGITS_REGEX } from 'constants/regex';

const DebtorsBirthdayForm = ({
  handleFormAnswers,
  userFormsAnswers,
  errors,
  debtorId
}: TDebtorsProps) => {
  const ageFieldName = `${debtorId}debtorAge`;
  const ageFieldState = getFieldStatus(userFormsAnswers, ageFieldName);
  const [age, setAge] = useState(ageFieldState?.value || '');

  useEffect(() => {
    handleFormAnswers(ageFieldName, age);
  }, [age]);

  return (
    <>
      <TextField
        value={age}
        fullWidth
        placeholder="Approximate age"
        errorMsg={getErrorMsg(errors, ageFieldName)}
        onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
          if (event.target.value && !ONLY_DIGITS_REGEX.test(event.target.value))
            return;
          setAge(event.target.value);
        }}
      />
    </>
  );
};

export default DebtorsBirthdayForm;
