import styles from './title.module.scss';
import Tooltip from '../tooltip/tooltip';

type TitleProps = {
  text: string;
  className?: string;
  tooltipText?: string;
  variant?: 'large' | 'medium';
  required?: boolean;
};

const Title = ({
  text,
  className,
  tooltipText,
  variant = 'large',
  required = false
}: TitleProps) => {
  const classes: Record<string, string> = {
    medium: styles.medium
  };

  const tooltipClasses: Record<string, string> = {
    medium: styles.tooltipMedium
  };

  return (
    <h2 className={`${className || ''}${classes[variant] || ''}`}>
      {text}
      {required && ' *'}
      {tooltipText && (
        <span className={`${styles.tooltip} ${tooltipClasses[variant] || ''}`}>
          <Tooltip text={tooltipText} />
        </span>
      )}
    </h2>
  );
};

export default Title;
