import Tooltip from '../tooltip/tooltip';
import styles from './subtitle.module.scss';

type SubtitleProps = {
  text: string | React.ReactNode;
  tooltipText?: string;
  style?: React.CSSProperties;
  className?: string;
  variant?: 'large' | 'small';
};

const Subtitle = ({
  text,
  tooltipText,
  style,
  className,
  variant = 'large'
}: SubtitleProps) => {
  const classes: Record<string, string> = {
    small: styles.small
  };

  const tooltipClasses: Record<string, string> = {
    small: styles.tooltipSmall
  };

  return (
    <p
      className={`${styles.questionSubTitle} ${className || ''} ${
        classes[variant] || ''
      }`}
      style={{ ...style }}
    >
      {text}
      {tooltipText && (
        <span className={`${styles.tooltip} ${tooltipClasses[variant] || ''}`}>
          <Tooltip text={tooltipText} />
        </span>
      )}
    </p>
  );
};

export default Subtitle;
