import { useEffect, useState } from 'react';
import { TQuestionFormProps } from '.';
import { getErrorMsg, getFieldStatus, maskPhoneNumber } from 'utils/form';
import { TextField } from '@mui/material';

const MobilePhoneForm = ({
  handleFormAnswers,
  errors,
  userFormsAnswers,
  placeholder,
  fieldName
}: TQuestionFormProps) => {
  const field = fieldName || '';
  const mobPhoneFieldState = getFieldStatus(userFormsAnswers, field);
  const [mobPhoneFieldValue, setMobPhoneValue] = useState(
    mobPhoneFieldState?.value || ''
  );

  useEffect(() => {
    handleFormAnswers(field, mobPhoneFieldValue);
  }, [mobPhoneFieldValue]);

  return (
    <>
      <TextField
        placeholder={placeholder}
        value={mobPhoneFieldValue}
        error={!!getErrorMsg(errors, field)}
        helperText={getErrorMsg(errors, field)}
        onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
          // eslint-disable-next-line no-useless-escape
          const value = event.target.value.replace(/[\)\( -]/gm, '');
          const maskedPhoneNumber = maskPhoneNumber(value);
          setMobPhoneValue(maskedPhoneNumber);
        }}
        inputProps={{
          maxLength: 14
        }}
        InputLabelProps={{ shrink: true }}
      />
    </>
  );
};

export default MobilePhoneForm;
