import { useEffect, useState } from 'react';
import TextField from 'components/shared/text-field/text-field';
import { TDebtorsProps } from '.';
import { getErrorMsg, getFieldStatus, maskSsnAndEinNumber } from 'utils/form';

const DebtorsSocSecForm = ({
  handleFormAnswers,
  userFormsAnswers,
  errors,
  debtorId,
  isBusinessDebtor
}: TDebtorsProps) => {
  const fieldName = isBusinessDebtor
    ? `${debtorId}debtorEIN`
    : `${debtorId}debtorSSN`;
  const fieldState = getFieldStatus(userFormsAnswers, fieldName);
  const [fieldValue, setValue] = useState(fieldState?.value || '');

  useEffect(() => {
    handleFormAnswers(fieldName, fieldValue);
  }, [fieldValue]);

  return (
    <TextField
      placeholder={`Enter ${
        isBusinessDebtor
          ? 'employer identification number'
          : 'social security number'
      }`}
      value={fieldValue}
      errorMsg={getErrorMsg(errors, fieldName)}
      onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
        const value = event.target.value.replace(/-/g, '');
        const maskedValue = maskSsnAndEinNumber(!!isBusinessDebtor, value);
        setValue(maskedValue);
      }}
      maxLength={isBusinessDebtor ? 10 : 11}
    />
  );
};

export default DebtorsSocSecForm;
