import { useEffect, useState } from 'react';
import { TDebtorsProps } from '.';
import { getErrorMsg, getFieldStatus, itemsToString } from 'utils/form';
import { Grid, TextField } from '@mui/material';

const DebtorsNameForm = ({
  handleFormAnswers,
  userFormsAnswers,
  errors,
  debtorId
}: TDebtorsProps) => {
  const debtorFirstNameFieldName = `${debtorId}deptorFirstName`;
  const debtorLastNameFieldName = `${debtorId}deptorLastName`;
  const debtorMiddleNameFieldName = `${debtorId}deptorMiddleName`;
  const debtorSuffixNameFieldName = `${debtorId}deptorSuffixName`;
  const debtorFullNamFieldName = `${debtorId}debtorFullName`;

  const debtorFirstNameFieldState = getFieldStatus(
    userFormsAnswers,
    debtorFirstNameFieldName
  );
  const debtorLastNameFieldState = getFieldStatus(
    userFormsAnswers,
    debtorLastNameFieldName
  );
  const debtorMiddleNameFieldState = getFieldStatus(
    userFormsAnswers,
    debtorMiddleNameFieldName
  );
  const debtorSuffixNameFieldState = getFieldStatus(
    userFormsAnswers,
    debtorSuffixNameFieldName
  );

  const [debtorFirstNameValue, setDebtorFirstNameValue] = useState(
    debtorFirstNameFieldState?.value || ''
  );
  const [debtorLastNameValue, setDebtorLastNameValue] = useState(
    debtorLastNameFieldState?.value || ''
  );
  const [debtorMiddleNameValue, setDebtorMiddleNameValue] = useState(
    debtorMiddleNameFieldState?.value || ''
  );
  const [debtorSuffixNameValue, setDebtorSuffixNameValue] = useState(
    debtorSuffixNameFieldState?.value || ''
  );

  const setAnswer = (fieldName: string, value: string) => {
    handleFormAnswers(fieldName, value);
    switch (fieldName) {
      case debtorFirstNameFieldName:
        setDebtorFirstNameValue(value);
        break;
      case debtorMiddleNameFieldName:
        setDebtorMiddleNameValue(value);
        break;
      case debtorLastNameFieldName:
        setDebtorLastNameValue(value);
        break;
      case debtorSuffixNameFieldName:
        setDebtorSuffixNameValue(value);
        break;
      default:
        break;
    }
  };

  useEffect(() => {
    const debtorFullName = itemsToString([
      debtorFirstNameValue,
      debtorLastNameValue,
      debtorMiddleNameValue,
      debtorSuffixNameValue
    ]);
    handleFormAnswers(debtorFullNamFieldName, debtorFullName);
  }, [
    debtorFirstNameValue,
    debtorLastNameValue,
    debtorMiddleNameValue,
    debtorSuffixNameValue
  ]);

  return (
    <>
      <Grid container spacing={2}>
        <Grid item xs={12} md={4}>
          <TextField
            placeholder="Jennifer"
            label="First Name"
            value={debtorFirstNameValue}
            error={!!getErrorMsg(errors, debtorFirstNameFieldName)}
            helperText={getErrorMsg(errors, debtorFirstNameFieldName)}
            onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
              const value = event.target.value;
              setAnswer(debtorFirstNameFieldName, value);
            }}
            InputLabelProps={{ shrink: true }}
          />
        </Grid>
        <Grid item xs={12} md={2}>
          <TextField
            label="Middle Initial"
            placeholder="Middle initial"
            value={debtorMiddleNameValue}
            error={!!getErrorMsg(errors, debtorMiddleNameFieldName)}
            helperText={getErrorMsg(errors, debtorMiddleNameFieldName)}
            onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
              const value = event.target.value;
              setAnswer(debtorMiddleNameFieldName, value);
            }}
            InputLabelProps={{ shrink: true }}
          />
        </Grid>
        <Grid item xs={12} md={4}>
          <TextField
            label="Last Name"
            placeholder="Parker"
            value={debtorLastNameValue}
            error={!!getErrorMsg(errors, debtorLastNameFieldName)}
            helperText={getErrorMsg(errors, debtorLastNameFieldName)}
            onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
              const value = event.target.value;
              setAnswer(debtorLastNameFieldName, value);
            }}
            InputLabelProps={{ shrink: true }}
          />
        </Grid>
        <Grid item xs={12} md={2}>
          <TextField
            label="Suffix"
            placeholder="Suffix"
            value={debtorSuffixNameValue}
            error={!!getErrorMsg(errors, debtorSuffixNameFieldName)}
            helperText={getErrorMsg(errors, debtorSuffixNameFieldName)}
            onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
              const value = event.target.value;
              setAnswer(debtorSuffixNameFieldName, value);
            }}
            InputLabelProps={{ shrink: true }}
          />
        </Grid>
      </Grid>
    </>
  );
};

export default DebtorsNameForm;
