import Button from 'components/shared/button/button';
import { Box } from '@mui/material';
import Subtitle from 'components/shared/subtitle/subtitle';
import Title from 'components/shared/title/title';
import styles from './success-modal.module.scss';
import { ReactComponent as SubmitImg } from 'icons/submit.svg';
import { ReactComponent as LineTop } from 'icons/line_top.svg';
import { ReactComponent as ItemTop } from 'icons/item_top.svg';
import { ReactComponent as LineBottom } from 'icons/line_bottom.svg';
import { ReactComponent as ItemBottom } from 'icons/item_bottom.svg';

type TSuccessProps = {
  onClose: () => void;
  setJudjmentQuestion: () => void;
};

const SuccessModal = ({ onClose, setJudjmentQuestion }: TSuccessProps) => {
  return (
    <div className={styles.formSubmitWrap}>
      <div className={styles.formSubmit}>
        <LineTop className={styles.LineTop} />
        <ItemTop className={styles.ItemTop} />
        <LineBottom className={styles.LineBottom} />
        <ItemBottom className={styles.ItemBottom} />
        <SubmitImg className={styles.SubmitImg} />
        <Title
          className={styles.submit_title}
          text="The form was submitted successfully"
        />
        <Box padding="10px 20px">
          <Subtitle
            className={styles.submit_text}
            text={
              <>
                Expect a confirmation email from{' '}
                <a
                  className={styles.mail_link}
                  href="mailto:forms@creditorsolutions.com"
                >
                  forms@creditorsolutions.com
                </a>
              </>
            }
          />
        </Box>
        <Button
          label="Submit Another Judgment"
          variant="primary"
          onClick={setJudjmentQuestion}
          className={styles.submit_btn}
        />
        <Button
          label="Go to main page"
          variant="secondary"
          onClick={onClose}
          className={styles.main_btn}
        />
      </div>
    </div>
  );
};

export default SuccessModal;
