import { useEffect, useState } from 'react';
import TextField from 'components/shared/text-field/text-field';
import { TDebtorsProps } from '.';
import { getErrorMsg, getFieldStatus } from 'utils/form';

const DebtorsBusinessNameForm = ({
  handleFormAnswers,
  userFormsAnswers,
  errors,
  debtorId
}: TDebtorsProps) => {
  const fieldName = `${debtorId}debtorBisName`;
  const fieldState = getFieldStatus(userFormsAnswers, fieldName);
  const [fieldValue, setValue] = useState(fieldState?.value || '');

  useEffect(() => {
    handleFormAnswers(fieldName, fieldValue);
  }, [fieldValue]);

  return (
    <TextField
      placeholder={`Debtor's name`}
      value={fieldValue}
      errorMsg={getErrorMsg(errors, fieldName)}
      onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
        const value = event.target.value;
        setValue(value);
      }}
      InputLabelProps={{ shrink: true }}
    />
  );
};

export default DebtorsBusinessNameForm;
