import styles from './radio-btn.module.scss';
import { ReactComponent as CheckIcon } from '../../../icons/check.svg';

interface IRadioBtnProps {
  label: string;
  variant?: 'medium' | 'small';
  checked?: boolean;
  isCurrent?: boolean;
  onClick: (event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => void;
}

const RadioBtn = ({
  variant = 'medium',
  label,
  isCurrent,
  checked,
  onClick
}: IRadioBtnProps) => {
  const isSmallRadioBTn = variant === 'small';
  const radioBtnWrapStyles = isSmallRadioBTn
    ? styles.radioBtnWrapSmall
    : styles.radioBtnWrapMedium;
  const isCurrentStyles = isCurrent ? styles.currentRadioBtn : '';

  return (
    <button
      className={`${radioBtnWrapStyles} ${styles.radioBtnWrapCommon} ${isCurrentStyles}`}
      onClick={(event: React.MouseEvent<HTMLButtonElement, MouseEvent>) =>
        onClick(event)
      }
    >
      <div className={`${styles.radio}`}>{checked && <CheckIcon />}</div>
      <p className={styles.label}>{label}</p>
    </button>
  );
};

export default RadioBtn;
