import { useEffect, useState, useRef } from 'react';
import ProgressBar from 'components/shared/progress-bar/progress-bar';
import RadioBtn from 'components/shared/radio-btn/radio-btn';
import {
  QUESTIONS_LIST,
  TAnswerValues,
  TQuestionsList,
  TQuestionWithId
} from 'constants/questions';
import styles from './form-page.module.scss';
import questionListStyles from '../../question-list/question-list.module.scss';
import QuestionList, {
  TQuestionButton,
  TUserAnswer
} from 'components/question-list/question-list';
import Button from 'components/shared/button/button';
import {
  QuestionForms,
  TFieldName,
  TInputAnswer
} from 'components/question-forms';
import { QuestionModals } from 'components/modals';
import schema from './validation';
import { TClientServiceAgreement } from 'components/question-forms/client-service-agreement-form';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import {
  getDebtorsLabel,
  getFieldLabel,
  mapUserAnswersToReviewModel
} from 'utils/form';
import { useBeforeunload } from 'react-beforeunload';
import { TDebtorsPostJudgmentFormProps } from 'components/question-forms/judgment-satisfaction';
import { getCookie, setCookie } from 'utils/cookie';
import textFieldStyles from '../../shared/text-field/text-field.module.scss';
import Title from 'components/shared/title/title';
import Subtitle from 'components/shared/subtitle/subtitle';
import { isMobile } from 'react-device-detect';
import formStyles from '../../question-forms/forms.module.scss';
import { Box, Grid } from '@mui/material';
import QuestionReviewTitle from 'components/question-review-title/question-review-title';
import QuestionReviewResponse from 'components/question-review-response/question-review-response';
import axios from 'axios';
import { CSF_API } from 'constants/links';
import Spinner from 'components/shared/spinner/spinner';
import SuccessModal from 'components/modals/success-modal/success-modal';
import ErrorContent from 'components/error-content/error-content';
import { redirectToMainPage } from 'utils/general';

const FormPage = () => {
  // Update version to clear cache
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [version, setVersion] = useState('2.0.1');
  const [isBusinessCreditorType, setBusinessCreditorType] = useState(false);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [listOfQuestions, setListOfQuestions] =
    useState<TQuestionsList>(QUESTIONS_LIST);
  const [currentQuestion, setCurrentQuestion] = useState<TQuestionWithId>(
    listOfQuestions[0]
  );
  // User options answers
  const [userAnswers, setUserAnswers] = useState<Array<TUserAnswer> | null>(
    null
  );
  // User form answers
  const [userFormsAnswers, setUserFormsAnswers] = useState<
    TInputAnswer[] | null
  >(null);
  // User form fields errors
  const [fieldErrors, setFieldErrors] = useState<Array<
    Record<string, string>
  > | null>(null);
  const [files, setFiles] = useState<File[] | null>(null);
  const [esign, setEsign] = useState<TClientServiceAgreement['esign'] | null>(
    null
  );
  // Chosen debtors
  const [debtors, setDebtors] = useState<string[]>([]);

  // User post judgment form answers
  const [allPostJudgPayments, setAllPostJudgPayments] =
    useState<TDebtorsPostJudgmentFormProps['allPostJudgPayments']>(null);
  const [disableNextBtn, setDisableNextBtn] = useState(false);

  //Current selected option
  const [selectedOption, setSelectedOption] = useState<TAnswerValues | null>(
    null
  );
  const [loading, setLoading] = useState<boolean>(false);
  const [showSuccessModal, setShowSuccessModal] = useState<boolean>(false);
  const [showSubmitError, setShowSubmitError] = useState<boolean>(false);

  const currentQuesionId = currentQuestion.id;
  const isESignForm = currentQuestion.formId === 'clientServiceAgreement';
  const questionListRef = useRef<HTMLDivElement | null>(null);
  const questionListTitleWrapRef = useRef<HTMLDivElement | null>(null);
  const nextQuesionIndex = currentQuesionId;
  const prevQuestionIndex = currentQuesionId - 2;
  const questionsLength = listOfQuestions.length;
  const questionsPaginationList: Array<TQuestionButton> = listOfQuestions
    .slice(
      0,
      (userAnswers?.length && userAnswers.length + 1) || nextQuesionIndex
    )
    .map(({ label, id }) => ({
      label,
      id
    }));

  const handleSaveState = () => {
    if (currentQuestion.id < questionsLength - 1) {
      const cache = {
        isModalOpen: isModalOpen,
        currentQuestion: currentQuestion,
        userAnswers: userAnswers,
        userFormsAnswers: userFormsAnswers,
        fieldErrors: fieldErrors,
        allPostJudgPayments: allPostJudgPayments,
        isBusinessCreditorType: isBusinessCreditorType,
        debtors: debtors
      };
      localStorage.setItem('cachedState', JSON.stringify(cache));
    }
  };

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  useBeforeunload((event: any) => {
    if (currentQuestion.id < questionsLength - 1) {
      handleSaveState();
    }
    event.preventDefault();
    const confirmationMessage = 'Hmmm';

    (event || window.event).returnValue = confirmationMessage; //Gecko + IE
    return confirmationMessage; //Webkit, Safari, Chrome
  });

  useEffect(() => {
    const questionsVersion = getCookie('questionVersion');
    if (questionsVersion !== version) {
      localStorage.clear();
      setCookie('questionVersion', version, 180);
    }
    const cachedStateString = localStorage.getItem('cachedState');

    if (cachedStateString) {
      const cachedState = JSON.parse(cachedStateString);
      cachedState?.isModalOpen && setIsModalOpen(cachedState.isModalOpen);
      cachedState?.currentQuestion &&
        setCurrentQuestion(cachedState.currentQuestion);
      cachedState?.userAnswers && setUserAnswers(cachedState.userAnswers);
      cachedState?.userFormsAnswers &&
        setUserFormsAnswers(cachedState.userFormsAnswers);
      cachedState?.fieldErrors && setFieldErrors(cachedState.fieldErrors);
      const cachedDebtors = cachedState?.debtors;
      setDebtors(cachedDebtors);
      cachedState?.allPostJudgPayments &&
        setAllPostJudgPayments(cachedState.allPostJudgPayments);
      setBusinessCreditorType(cachedState?.isBusinessCreditorType || false);

      // const generatedQuestion =
      //   cachedDebtors &&
      //   generateNewQuestions(cachedDebtors as string[], isBusinessCreditorType);

      // if (generatedQuestion) {
      //   setListOfQuestions(generatedQuestion);
      // }
    }
    // else {
    //   // Generate questions with deptor:
    //   if (!debtors.length) {
    //     setListOfQuestions(
    //       generateNewQuestions(['individual'], isBusinessCreditorType)
    //     );
    //   }
    // }
  }, []);

  useEffect(() => {
    if (questionListRef.current) {
      questionListRef.current.scrollTop = questionListRef.current.scrollHeight;
    }
  }, [userAnswers]);

  useEffect(() => {
    // Cut questions according to creditor type
    if (isBusinessCreditorType) {
      const businessQuestions = listOfQuestions.filter(
        (question) => !question.isDisableForBusiness
      );
      // Updates ids:
      const updatedIds = businessQuestions.map((el, ind) => ({
        ...el,
        id: ind + 1
      }));
      setListOfQuestions(updatedIds);
    } else {
      const isQuestionsExist = listOfQuestions.some(
        (el) => el.isDisableForBusiness
      );
      if (!isQuestionsExist) {
        const missingQuestions = QUESTIONS_LIST.filter(
          (el) => el.isDisableForBusiness
        );
        const missingIndexes = missingQuestions.map((el) => {
          const index = QUESTIONS_LIST.indexOf(el);
          return index;
        });
        const newQuestions = [...listOfQuestions];
        missingIndexes.forEach((el) => {
          newQuestions.splice(el, 0, QUESTIONS_LIST[el]);
        });
        // Updates ids:
        const updatedIds = newQuestions.map((el, ind) => ({
          ...el,
          id: ind + 1
        }));
        setListOfQuestions(updatedIds);
      }
    }
  }, [isBusinessCreditorType]);

  useEffect(() => {
    //Set current selected option is already responsed
    const currentOptionAnswer = userAnswers?.find(
      (el) => el.title === currentQuestion.title
    );
    // Doublecheck debtor if user change(delete) type in debtor form:
    const firstDebtor = debtors?.[0];
    const debtorTypeAnswer = userAnswers?.find(
      (el) =>
        el.title ===
        'Was this Judgment Awarded Against an Individual or a Business?'
    );
    if (
      debtorTypeAnswer &&
      firstDebtor &&
      typeof debtorTypeAnswer?.answer === 'string' &&
      debtorTypeAnswer?.answer?.toLowerCase() !== firstDebtor?.toLowerCase()
    ) {
      debtorTypeAnswer.answer =
        firstDebtor === 'individual' ? 'Individual' : 'Business';
      debtorTypeAnswer.answerId = firstDebtor;
      setUserAnswers((prevState) => {
        if (prevState) {
          return [
            ...prevState.filter((el) => el.title !== debtorTypeAnswer.title),
            debtorTypeAnswer
          ];
        }
        return prevState;
      });
    }
    setSelectedOption(
      typeof currentOptionAnswer?.answer === 'string'
        ? {
            value: currentOptionAnswer?.answer,
            answerId: currentOptionAnswer.answerId
          }
        : null
    );
  }, [currentQuestion]);

  const qustionListGradientListener = () => {
    const questionScrollTop = questionListRef?.current?.scrollTop || 0;

    if (questionListTitleWrapRef.current) {
      questionScrollTop === 0
        ? questionListTitleWrapRef.current.classList.remove(
            styles.upperGradienOn
          )
        : questionListTitleWrapRef.current.classList.add(styles.upperGradienOn);
    }

    if (
      questionListRef?.current?.offsetHeight &&
      questionListRef?.current?.scrollTop &&
      questionListRef?.current?.scrollHeight &&
      questionListTitleWrapRef.current
    ) {
      if (
        questionListRef?.current?.offsetHeight +
          questionListRef?.current?.scrollTop >=
        questionListRef?.current?.scrollHeight
      ) {
        questionListTitleWrapRef.current.classList.remove(
          styles.downGradientOn
        );
      } else {
        questionListTitleWrapRef.current.classList.add(styles.downGradientOn);
      }
    }
  };

  const isValidateOnModal = (answer: string) => {
    const YES_REGEX = /yes/i;
    const NO_REGEX = /no/i;
    const UNSURE_REGEX = /unsure/i;

    if (
      currentQuestion.modalId === 'debtorPayments' &&
      YES_REGEX.test(answer)
    ) {
      return false;
    }
    if (
      currentQuestion.modalId === 'validNYStateJudgment' &&
      NO_REGEX.test(answer)
    ) {
      return false;
    }
    if (
      currentQuestion.modalId === 'vacatedOvertuned' &&
      YES_REGEX.test(answer)
    )
      return false;
    if (
      (currentQuestion.modalId === 'dischargedBankruptcy' ||
        currentQuestion.modalId === 'judgmentAwarded') &&
      (YES_REGEX.test(answer) || UNSURE_REGEX.test(answer))
    ) {
      return false;
    }
    return true;
  };

  const setNextQuestion = () => {
    if (questionsLength !== nextQuesionIndex) {
      setCurrentQuestion(listOfQuestions[nextQuesionIndex]);
      setSelectedOption(null);
    }
  };

  const setPrevQuestion = () => {
    setCurrentQuestion(listOfQuestions[prevQuestionIndex]);
  };

  const handleSubmit = () => {
    if (fieldErrors) return;
    // TODO: map to payload model
    const mappedUserAnswers = mapUserAnswersToReviewModel(
      listOfQuestions,
      userFormsAnswers,
      userAnswers,
      allPostJudgPayments,
      files,
      isBusinessCreditorType
    );
    setLoading(true);
    axios
      .post(CSF_API, {
        answers: mappedUserAnswers
      })
      .then(() => {
        setShowSuccessModal(true);
        setShowSubmitError(false);
      })
      .catch(() => {
        setShowSuccessModal(false);
        setShowSubmitError(true);
      })
      .finally(() => {
        setLoading(false);
      });
  };

  const handleNextQuestion = async () => {
    const answer = selectedOption?.value;
    const answerId = selectedOption?.answerId;

    if (answerId === 'individualCreditorType') {
      setBusinessCreditorType(false);
    }
    if (answerId === 'businessCreditorType') {
      setBusinessCreditorType(true);
    }

    if (answer && !isValidateOnModal(answer)) {
      setIsModalOpen(true);
      return;
    }

    setFieldErrors(null);

    const userAnswer = userAnswers?.find(
      (answ) => currentQuestion.id === answ.id
    ) || {
      id: currentQuestion.id,
      answer: answer ? answer : [],
      title: currentQuestion.title
    };

    if (answer) {
      userAnswer.answer = answer;
    }

    if (answerId) {
      userAnswer.answerId = answerId;
    }

    // Validate user answers from fields:
    if (currentQuestion.formId) {
      const inputsAnswers = userFormsAnswers?.filter(
        (el) => el.formId === currentQuestion.formId
      );

      if (inputsAnswers && inputsAnswers?.length) {
        const errors: Array<Record<string, string>> = [];
        const validateObj: Record<string, unknown> = {};
        validateObj.currentForm = currentQuestion.formId;
        validateObj.isBusinessCreditorType = isBusinessCreditorType;

        inputsAnswers.forEach(({ fieldName, value }) => {
          validateObj[fieldName] = value ? value : undefined;
          validateObj.currentField = fieldName;
          // if (value) {
          //   const answ: Record<string, string> = {};
          //   answ[fieldName] = value;
          //  TODO: delete duplicates
          // if (typeof userAnswer.answer !== 'string') {
          //   userAnswer.answer.forEach((el) => {
          //     if (el.fieldName === fieldName) {
          //       el.value = value;
          //     }
          //   });
          // }
          // (userAnswer.answer as TInputAnswer[]).push({ fieldName, value });
          // }
          // Set answer as a string
          // if (value && inputsAnswers.length === 1) {
          //   userAnswer.answer = value;
          // }
        });

        // Fields Validation:
        await schema
          .validate(validateObj, { abortEarly: false })
          .catch((error) => {
            // eslint-disable-next-line @typescript-eslint/no-explicit-any
            error.inner.forEach((e: any) => {
              errors.push({
                fieldName: e.path,
                errorMsg: e.message
              });
            });
          });

        if (errors.length) {
          setFieldErrors(errors);
          return;
        }
      }
      // Not set next question if user didn't choose anything
    } else if (!userAnswer.answer.length && !currentQuestion.isReviewPage) {
      if (currentQuestion.formId === 'judgmentSatisfaction') {
        const isAnswered = userFormsAnswers?.find(
          (el) => el.fieldName === 'judgmentSatisfaction'
        );
        if (!isAnswered) {
          return;
        }
      }
      return;
    }

    setUserAnswers((prevState) => {
      if (prevState) {
        return [
          ...prevState.filter((el) => el.title !== userAnswer.title),
          userAnswer
        ];
      }
      return [userAnswer];
    });

    // Changed first debtor:
    if (currentQuestion.deptorId === 0 && userAnswer.answerId) {
      setDebtors([userAnswer.answerId as string, ...debtors.slice(1)]);
      // const newQuestions = generateNewQuestions(
      //   changedFirstDebtor,
      //   isBusinessCreditorType
      // );
      // setListOfQuestions(newQuestions);
      // setCurrentQuestion(newQuestions[nextQuesionIndex]);
      // return;
    }

    // // Generated new questions with co-debtors:
    // const updateQuestionsAccordingToDebstor = (newDeptors: string[]) => {
    //   setDebtors(newDeptors);
    //   const newQuestions = generateNewQuestions(
    //     newDeptors,
    //     isBusinessCreditorType
    //   );
    //   setListOfQuestions(newQuestions);
    //   setCurrentQuestion(newQuestions[nextQuesionIndex]);
    // };
    // if (
    //   currentQuestion.deptorId &&
    //   currentQuestion.deptorId >= 1 &&
    //   userAnswer.answerId !== 'not'
    // ) {
    //   //  TODO: delete user answers
    //   const newDeptors = [...debtors.slice(0, currentQuestion.deptorId)];
    //   newDeptors.push(userAnswer.answerId as string);
    //   updateQuestionsAccordingToDebstor(newDeptors);
    //   return;
    // } else if (
    //   currentQuestion.deptorId &&
    //   currentQuestion.deptorId >= 1 &&
    //   userAnswer.answerId === 'not'
    // ) {
    //   const newDeptors = [...debtors.slice(0, currentQuestion.deptorId)];
    //   updateQuestionsAccordingToDebstor(newDeptors);
    //   return;
    // }

    // Save cache before two last questions
    if (listOfQuestions?.[nextQuesionIndex]?.formId === 'uploadDocuments') {
      handleSaveState();
    }

    // Set next question
    setNextQuestion();
    if (isESignForm) {
      handleSubmit();
    }
  };

  const handleSetQuestion = (ind: number) => {
    setCurrentQuestion(listOfQuestions[ind]);
  };

  const QuestionFormUI =
    currentQuestion.formId && QuestionForms[currentQuestion.formId];

  const QuestionModalUI =
    currentQuestion.modalId && QuestionModals[currentQuestion.modalId];

  const subTitle = currentQuestion.subTitle && (
    <Subtitle
      className={formStyles.subtitle}
      text={currentQuestion.subTitle}
      tooltipText={currentQuestion.subtitleTooltip}
    />
  );

  const setJudjmentQuestion = () => {
    setCurrentQuestion(QUESTIONS_LIST[0]);
    setUserAnswers([]);
    setUserFormsAnswers([]);
    setAllPostJudgPayments(null);
    if (isModalOpen) setIsModalOpen(false);
    if (showSuccessModal) setShowSuccessModal(false);
  };

  const handleFormAnswers = (
    fieldName: TFieldName,
    value: string | undefined
  ) => {
    if (value) {
      setFieldErrors(
        (prevState) =>
          prevState?.filter((error) => error.fieldName !== fieldName) ||
          prevState
      );
    }
    const userAnswer: TInputAnswer = userFormsAnswers?.find(
      (answ) => fieldName === answ.fieldName
    ) || {
      fieldName,
      value: value,
      questionId: currentQuestion.id,
      formId: currentQuestion.formId
    };
    userAnswer.value = value;
    setUserFormsAnswers((prevState) => {
      if (prevState) {
        return [
          ...prevState.filter((el) => el.fieldName !== fieldName),
          userAnswer
        ];
      }
      return [userAnswer];
    });
  };

  const isProgressListHidden = isMobile && currentQuesionId === 1;

  const questionOptions = currentQuestion.answers && (
    <ul className={styles.responsesList}>
      {currentQuestion.answers.map((answer) => {
        const isChecked = selectedOption?.value === answer.value;
        return (
          <li key={answer.value}>
            <RadioBtn
              label={answer.value}
              key={answer.value}
              checked={isChecked}
              isCurrent={isChecked}
              onClick={() => {
                // Disable next btn on the first question
                if (currentQuesionId === 1 && answer.value === 'Decline') {
                  setDisableNextBtn(true);
                } else if (
                  currentQuesionId === 1 &&
                  answer.value === 'Accept'
                ) {
                  setDisableNextBtn(false);
                }
                setSelectedOption(answer);
              }}
            />
          </li>
        );
      })}
    </ul>
  );

  const mappedUserAnswers = currentQuestion.isReviewPage
    ? mapUserAnswersToReviewModel(
        listOfQuestions,
        userFormsAnswers,
        userAnswers,
        allPostJudgPayments,
        files,
        isBusinessCreditorType
      )
    : [];

  const handleEditQuestion = (index: number) => {
    const questionTitle = mappedUserAnswers?.[index].questionTitle;
    const neededIndex = listOfQuestions.findIndex(
      (el) => el.title === questionTitle
    );
    if (neededIndex >= 0) {
      setCurrentQuestion(listOfQuestions[neededIndex]);
    }
  };

  if (showSuccessModal)
    return (
      <SuccessModal
        setJudjmentQuestion={setJudjmentQuestion}
        onClose={() => {
          setShowSuccessModal(false);
          redirectToMainPage();
        }}
      />
    );

  const reviewPage = currentQuestion.isReviewPage && (
    <div className={styles.reviewContent}>
      {mappedUserAnswers?.map(
        (
          { questionTitle, isForm, userAnswers, postJudgments, formId },
          index
        ) => {
          const questionReviewTitle = `${index + 1}. ${questionTitle}`;

          if (!isForm) {
            return (
              <div
                className={styles.reviewAnswerContent}
                key={`${questionTitle}-${index}`}
              >
                <QuestionReviewTitle
                  title={questionReviewTitle}
                  onEdit={() => handleEditQuestion(index)}
                />
                <QuestionReviewResponse label={userAnswers?.[0] as string} />
              </div>
            );
          }
          if (formId === 'debtorInformation') {
            const debtorsFields = debtors.map((debtorType, ind) => {
              const filteredDebtors = (userAnswers as TInputAnswer[]).filter(
                (answ) => answ.fieldName.includes(`${debtorType}${ind}`)
              );
              return filteredDebtors;
            });
            return (
              <div
                className={styles.reviewAnswerContent}
                key={`${questionTitle}-${index}`}
              >
                {debtorsFields.map((debtorArr, debtorInd) => {
                  const debtorTitle = `${
                    debtorInd > 0 ? 'Co-' : `${index + 1}. `
                  }Debtor Information`;
                  return (
                    <Box className={styles.debtorsBox} key={debtorTitle}>
                      <QuestionReviewTitle
                        title={debtorTitle}
                        onEdit={() => handleEditQuestion(index)}
                      />
                      {debtorArr.map(({ fieldName, value }) => {
                        return (
                          <QuestionReviewResponse
                            key={fieldName}
                            label={getDebtorsLabel(fieldName) || ''}
                            value={value}
                          />
                        );
                      })}
                    </Box>
                  );
                })}
              </div>
            );
          }
          return (
            <Box key={formId} className={styles.reviewAnswerContent}>
              <QuestionReviewTitle
                title={questionReviewTitle}
                onEdit={() => handleEditQuestion(index)}
              />
              {(userAnswers as TInputAnswer[]).map(({ fieldName, value }) => {
                return (
                  <QuestionReviewResponse
                    key={fieldName}
                    label={
                      getFieldLabel[fieldName] ? getFieldLabel[fieldName] : ''
                    }
                    value={value}
                  />
                );
              })}
              {postJudgments?.map(({ date, amount }, index) => {
                return (
                  <div
                    key={index}
                    style={{
                      marginTop: 10
                    }}
                  >
                    <Grid container spacing={2}>
                      <Grid item>
                        <QuestionReviewResponse label="Date" value={date} />
                      </Grid>
                      <Grid item>
                        <QuestionReviewResponse label="Amount" value={amount} />
                      </Grid>
                    </Grid>
                  </div>
                );
              })}
            </Box>
          );
        }
      )}
    </div>
  );

  return (
    <LocalizationProvider dateAdapter={AdapterDayjs}>
      <Spinner loading={loading} />
      <div className={styles.formPageWrap}>
        {QuestionModalUI && (
          <QuestionModalUI
            isOpen={isModalOpen}
            onClose={() => setIsModalOpen(false)}
            submitAnotherJudj={setJudjmentQuestion}
          />
        )}
        <div
          className={`${styles.formContainer} ${
            isESignForm ? styles.esignForm : ''
          }`}
        >
          {showSubmitError ? (
            <ErrorContent onSubmit={handleSubmit} />
          ) : (
            <>
              <div className={styles.currentQuestionWrap}>
                <ProgressBar
                  label={currentQuestion.progressLabel}
                  current={currentQuestion.id}
                  max={questionsLength}
                  labelOfCurrent=""
                />
                {currentQuestion.formId !== 'debtorInformation' && (
                  <div className={styles.questionContainer}>
                    <Title
                      text={currentQuestion.title}
                      tooltipText={currentQuestion.titleTooltip}
                    />
                    {subTitle}
                    {questionOptions}
                    {currentQuestion.helperText && (
                      <p className={textFieldStyles.note}>
                        {currentQuestion.helperText}
                      </p>
                    )}
                  </div>
                )}
                {reviewPage}
                {QuestionFormUI && (
                  <div className={styles.questionFormUI}>
                    <QuestionFormUI
                      errors={fieldErrors}
                      handleFormAnswers={handleFormAnswers}
                      userFormsAnswers={userFormsAnswers}
                      userAnswers={userAnswers}
                      files={files}
                      setFiles={setFiles}
                      setEsign={setEsign}
                      esign={esign}
                      formId={currentQuestion.formId}
                      allPostJudgPayments={allPostJudgPayments}
                      setAllPostJudgPayments={setAllPostJudgPayments}
                      isBusinessCreditorType={isBusinessCreditorType}
                      currentQuestion={currentQuestion}
                      handleNextQuestion={handleNextQuestion}
                      debtors={debtors}
                      setDebtors={setDebtors}
                    />
                  </div>
                )}
                <div
                  className={`${
                    currentQuesionId === 1
                      ? `${styles.formFooter} ${styles.jusC}`
                      : styles.formFooter
                  }`}
                >
                  <Button
                    variant="secondary"
                    label={isMobile ? 'Prev' : 'Previous'}
                    withArrowBack
                    onClick={setPrevQuestion}
                    className={`${
                      currentQuesionId === 1 ? styles.displayNone : ''
                    }`}
                  />
                  <Button
                    label={isESignForm ? 'Submit' : 'Next'}
                    withArrow={!isESignForm}
                    onClick={handleNextQuestion}
                    disabled={disableNextBtn}
                    className={`${
                      currentQuesionId === 1 ? styles.fullWidth : ''
                    }`}
                  />
                </div>
              </div>
              {!isProgressListHidden && (
                <div
                  ref={questionListTitleWrapRef}
                  className={styles.questionsListTitleWrap}
                >
                  <h4 className={questionListStyles.title}>PROGRESS BAR</h4>
                  <div
                    ref={questionListRef}
                    className={styles.questionsListWrap}
                    onScroll={qustionListGradientListener}
                  >
                    <div className={styles.questionsListContent}>
                      <QuestionList
                        title=""
                        list={questionsPaginationList}
                        userAnswers={userAnswers}
                        currentQuestionId={currentQuesionId}
                        onClick={handleSetQuestion}
                      />
                    </div>
                  </div>
                </div>
              )}
            </>
          )}
        </div>
      </div>
    </LocalizationProvider>
  );
};

export default FormPage;
