import { useEffect, useState } from 'react';
import { TQuestionFormProps } from '.';
import { getFieldStatus, getFormatDate } from 'utils/form';
import dayjs, { Dayjs } from 'dayjs';
import { Grid, TextField as MUITextField, Box, Button } from '@mui/material';
import { DesktopDatePicker } from '@mui/x-date-pickers/DesktopDatePicker';
import { TFormId } from 'constants/questions';
import RadioBtn from 'components/shared/radio-btn/radio-btn';
import Subtitle from 'components/shared/subtitle/subtitle';

export type TJudgmentSatisfactionProps = {
  formId: TFormId | string;
  amount: string;
  date: string;
  id: number;
};

export type TDebtorsPostJudgmentFormProps = TQuestionFormProps & {
  allPostJudgPayments: TJudgmentSatisfactionProps[] | null;
  setAllPostJudgPayments: React.Dispatch<
    React.SetStateAction<TDebtorsPostJudgmentFormProps['allPostJudgPayments']>
  >;
};

const JudgmentSatisfaction = ({
  formId,
  userFormsAnswers,
  allPostJudgPayments,
  setAllPostJudgPayments,
  handleFormAnswers
}: TDebtorsPostJudgmentFormProps) => {
  const fieldName = formId || 'jugmentSatisfaction';
  const judgmentSatisfactionFieldState = getFieldStatus(
    userFormsAnswers,
    fieldName
  );

  const [judgmnetSatisfactionAnswer, setJudgmentSatisfactionAsnwer] = useState(
    judgmentSatisfactionFieldState?.value || ''
  );
  const filteredLocalPayments = allPostJudgPayments?.filter(
    ({ formId }) => formId === formId
  ) || [
    {
      formId: formId || '',
      amount: '',
      date: '',
      id: 1
    }
  ];

  const answers = [
    {
      value: 'Yes'
    },
    {
      value: 'No'
    }
  ];

  const [postJudgPayments, setPostJudgPayments] = useState(
    filteredLocalPayments
  );

  useEffect(() => {
    setAllPostJudgPayments((prevState) => {
      if (prevState) {
        const allPaymentExceptCurrent = prevState.filter(
          ({ formId }) => formId !== formId
        );
        return [...allPaymentExceptCurrent, ...postJudgPayments];
      }
      if (!prevState) {
        return [...postJudgPayments];
      }
      return prevState;
    });
    handleFormAnswers(fieldName, judgmnetSatisfactionAnswer);
    if (judgmnetSatisfactionAnswer === 'No') {
      setAllPostJudgPayments(null);
    }
  }, [postJudgPayments, judgmnetSatisfactionAnswer]);

  return (
    <>
      <ul>
        {answers.map(({ value }) => {
          const isChecked = judgmnetSatisfactionAnswer === value;
          return (
            <li
              style={{
                marginBottom: 10
              }}
              key={value}
            >
              <RadioBtn
                label={value}
                key={value}
                checked={isChecked}
                isCurrent={isChecked}
                onClick={() => {
                  setJudgmentSatisfactionAsnwer(value);
                }}
              />
            </li>
          );
        })}
      </ul>
      {judgmnetSatisfactionAnswer === 'Yes' && (
        <Box
          style={{
            marginTop: 34
          }}
        >
          <Subtitle
            text={
              'Set forth the date(s) and amount(s) of any post-judgment payments made towards satisfaction of this judgment.'
            }
            style={{
              marginBottom: 16,
              fontSize: '16px'
            }}
          />
          {postJudgPayments.map(({ amount, date, id }, ind, arr) => {
            const isLastOne = id === arr.length;
            return (
              <div key={`${id}`}>
                <Grid container spacing={2} mb={2}>
                  <Grid item xs={12} sm={6}>
                    <DesktopDatePicker
                      inputFormat="MM/DD/YYYY"
                      value={dayjs(date)}
                      disableFuture
                      onChange={(newValue: Dayjs | null) => {
                        if (newValue && newValue.isValid()) {
                          const formatedValue = getFormatDate(newValue);
                          setPostJudgPayments((prevState) => {
                            const currentPayment = prevState.find(
                              (p) => p.id === id
                            );
                            if (currentPayment) {
                              currentPayment.date = formatedValue;
                              return [
                                ...prevState.slice(0, ind),
                                currentPayment,
                                ...prevState.slice(ind + 1)
                              ];
                            }
                            return prevState;
                          });
                          return;
                        }
                        setPostJudgPayments((prevState) => {
                          const currentPayment = prevState.find(
                            (p) => p.id === id
                          );
                          if (currentPayment) {
                            currentPayment.date = '';
                            return [
                              ...prevState.slice(0, ind),
                              currentPayment,
                              ...prevState.slice(ind + 1)
                            ];
                          }
                          return prevState;
                        });
                      }}
                      renderInput={(params) => (
                        <MUITextField
                          {...params}
                          className="datePickerError"
                          fullWidth
                          error={false}
                        />
                      )}
                    />
                  </Grid>
                  <Grid item xs={12} sm={6}>
                    <MUITextField
                      fullWidth
                      placeholder="Amount"
                      value={amount}
                      onChange={(
                        event: React.ChangeEvent<HTMLInputElement>
                      ) => {
                        const value = event.target.value;
                        setPostJudgPayments((prevState) => {
                          const currentPayment = prevState.find(
                            (p) => p.id === id
                          );
                          if (currentPayment) {
                            currentPayment.amount = value;
                            return [
                              ...prevState.slice(0, ind),
                              currentPayment,
                              ...prevState.slice(ind + 1)
                            ];
                          }
                          return prevState;
                        });
                      }}
                    />
                  </Grid>
                </Grid>
                {isLastOne && (
                  <Grid container spacing={2} justifyContent="flex-end">
                    {postJudgPayments.length > 1 && (
                      <Grid item>
                        <Button
                          onClick={() => {
                            const copyJudgs = [...postJudgPayments];
                            copyJudgs.splice(copyJudgs.length - 1, 1);
                            setPostJudgPayments(copyJudgs);
                          }}
                          style={{
                            color: '#D64A41'
                          }}
                        >
                          Delete
                        </Button>
                      </Grid>
                    )}
                    <Grid item>
                      <Button
                        onClick={() => {
                          setPostJudgPayments((prevState) => {
                            const newPaymentFields = {
                              formId: formId || '',
                              amount: '',
                              datejs: '',
                              date: '',
                              id: arr.length + 1
                            };
                            const copyOfState = [
                              ...prevState,
                              newPaymentFields
                            ];

                            return copyOfState;
                          });
                        }}
                      >
                        Add
                      </Button>
                    </Grid>
                  </Grid>
                )}
              </div>
            );
          })}
        </Box>
      )}
    </>
  );
};

export default JudgmentSatisfaction;
