import styles from './button.module.scss';
import { ReactComponent as ArrowIcon } from '../../../icons/arrow.svg';
import { ReactComponent as ArrowIconBack } from '../../../icons/arrow-left.svg';

interface IButtonProps {
  label: string;
  variant?: 'primary' | 'secondary';
  onClick?: () => void;
  withArrow?: boolean;
  withArrowBack?: boolean;
  disabled?: boolean;
  className?: string;
}

const Button = ({
  label,
  variant = 'primary',
  onClick,
  withArrow,
  withArrowBack,
  disabled,
  className = ''
}: IButtonProps) => {
  const variantStyle = {
    primary: styles.primary,
    secondary: styles.secondary
  };
  return (
    <button
      className={`${styles.button} ${variantStyle[variant]} ${className}`}
      onClick={onClick}
      disabled={disabled}
    >
      {withArrowBack && (
        <span className={styles.arrowBack}>
          <ArrowIconBack />
        </span>
      )}
      <span className={styles.label}>{label}</span>
      {withArrow && (
        <span className={styles.arrow}>
          <ArrowIcon />
        </span>
      )}
    </button>
  );
};

export default Button;
