import {
  DATE_REGEX,
  DEBTOR_AGE,
  PHONE_REGEX,
  SSN_AND_EIN
  // ZIPCODE_REGEX
} from '../../../constants/regex';
import * as yup from 'yup';

export const requiredStringSchema = yup
  .string()
  .required('Please fill in required field before proceeding');

const ssnStringSchema = yup.string().matches(SSN_AND_EIN, {
  message: 'Please fill in valid value before proceeding'
});

const debtorAgeStringSchema = yup.string().matches(DEBTOR_AGE, {
  message: 'Please fill in valid value before proceeding'
});

