const ssnFieldsSchema = {
  // debtorSSN: debtorSSNSchema,
  // coDeptor1SSN: coDeptor1SSNSchema,
  // coDeptor2SSN: coDeptor2SSNSchema,
  // coDeptor3SSN: coDeptor3SSNSchema,
  // coDeptor4SSN: coDeptor4SSNSchema,
  debtorSSN: ssnStringSchema,
  debtorSSN1: ssnStringSchema,
  debtorSSN2: ssnStringSchema,
  debtorSSN3: ssnStringSchema,
  debtorSSN4: ssnStringSchema
};

const deborAges = {
  individual0debtorAge: debtorAgeStringSchema,
  individual1debtorAge: debtorAgeStringSchema,
  individual2debtorAge: debtorAgeStringSchema,
  individual3debtorAge: debtorAgeStringSchema,
  individual4debtorAge: debtorAgeStringSchema
};

const schema = yup.object({
  currentForm: yup.string(),
  currentField: yup.string(),
  isBusinessCreditorType: yup.boolean(),
  businessCreditorName: yup.string().when('currentForm', {
    is: 'creditorInfo',
    then: yup.string().when('isBusinessCreditorType', {
      is: true,
      then: requiredStringSchema
    })
  }),
  creditorName: yup.string().when('currentForm', {
    is: 'creditorInfo',
    then: yup.string().when('isBusinessCreditorType', {
      is: false,
      then: requiredStringSchema
    })
  }),
  creditorLastName: yup.string().when('currentForm', {
    is: 'creditorInfo',
    then: yup.string().when('isBusinessCreditorType', {
      is: false,
      then: requiredStringSchema
    })
  }),
  creditorContactName: yup.string().when('currentForm', {
    is: 'creditorInfo',
    then: requiredStringSchema
  }),
  creditorContactLastName: yup.string().when('currentForm', {
    is: 'creditorInfo',
    then: requiredStringSchema
  }),
  // contactName: yup.string().when('currentForm', {
  //   is: 'contactName',
  //   then: requiredStringSchema
  // }),
  email: yup
    .string()
    .email()
    .when('currentForm', {
      is: 'creditorInfo',
      then: yup.string().when('isBusinessCreditorType', {
        is: false,
        then: requiredStringSchema
      })
    }),
  bisEmail: yup.string().when('currentForm', {
    is: 'creditorInfo',
    then: yup.string().when('isBusinessCreditorType', {
      is: true,
      then: requiredStringSchema
    })
  }),
  businessTitle: yup.string().when('currentForm', {
    is: 'creditorInfo',
    then: yup.string().when('isBusinessCreditorType', {
      is: true,
      then: requiredStringSchema
    })
  }),
  mobPhone: yup.string().when('currentForm', {
    is: 'creditorInfo',
    then: yup.string().when('isBusinessCreditorType', {
      is: false,
      then: requiredStringSchema.matches(PHONE_REGEX, {
        message: 'Mobile Phone MUST BE A VALID phone'
      })
    })
  }),
  bisPhone: yup.string().when('currentForm', {
    is: 'creditorInfo',
    then: yup.string().when('isBusinessCreditorType', {
      is: true,
      then: requiredStringSchema
    })
  }),
  altPhone: yup.string().matches(PHONE_REGEX, {
    message: 'Alt. Phone MUST BE A VALID phone'
  }),
  creditorAddress: yup.string().when('currentForm', {
    is: 'creditorInfo',
    then: yup.string().when('isBusinessCreditorType', {
      is: false,
      then: requiredStringSchema
    })
  }),
  bisAddress: yup.string().when('currentForm', {
    is: 'creditorInfo',
    then: yup.string().when('isBusinessCreditorType', {
      is: true,
      then: requiredStringSchema
    })
  }),
  // zipCode: yup.string().when('currentForm', {
  //   is: 'creditorAddress',
  //   then: requiredStringSchema.matches(ZIPCODE_REGEX, {
  //     message: 'ZIP Code MUST BE A VALID ZIP Code'
  //   })
  // }),
  // state: yup.string().when('currentForm', {
  //   is: 'creditorAddress',
  //   then: requiredStringSchema
  // }),
  // city: yup.string().when('currentForm', {
  //   is: 'creditorAddress',
  //   then: requiredStringSchema
  // }),
  // addressLine1: yup.string().when('currentForm', {
  //   is: 'creditorAddress',
  //   then: requiredStringSchema
  // }),
  // addressLine2: yup.string(),
  indexNumber: yup.string().when('currentForm', {
    is: 'judgmentInfo',
    then: requiredStringSchema
  }),
  courtFilingDate: yup.string().when('currentForm', {
    is: 'judgmentInfo',
    then: requiredStringSchema.matches(DATE_REGEX, {
      message: 'Date MUST BE A VALID Date'
    })
  }),
  judgmentAmount: yup.string().when('currentForm', {
    is: 'judgmentInfo',
    then: requiredStringSchema
  }),
  filingCounty: yup.string().when('currentForm', {
    is: 'judgmentInfo',
    then: requiredStringSchema
  }),
  filingCourt: yup.string().when('currentForm', {
    is: 'judgmentInfo',
    then: requiredStringSchema
  }),
  judgmentReason: yup.string().when('currentForm', {
    is: 'judgmentReason',
    then: requiredStringSchema
  }),
  eSign: yup.string().when('currentForm', {
    is: 'clientServiceAgreement',
    then: requiredStringSchema
  }),
  firstName: yup.string().when('currentForm', {
    is: 'clientServiceAgreement',
    then: requiredStringSchema
  }),
  lastName: yup.string().when('currentForm', {
    is: 'clientServiceAgreement',
    then: requiredStringSchema
  }),
  // date: yup.string().when('currentForm', {
  //   is: 'clientServiceAgreement',
  //   then: requiredStringSchema.matches(DATE_REGEX, {
  //     message: 'Date MUST BE A VALID Date'
  //   })
  // }),
  natureOfAction: yup.string().when('currentForm', {
    is: 'natureOfAction',
    then: requiredStringSchema
  }),
  ...ssnFieldsSchema,
  ...deborAges
});

export default schema;
