import Button from 'components/shared/button/button';
import { redirectToMainPage } from 'utils/general';
import Modal, { TModalProps } from '../modal';
import ModalActions from '../modal-actions';
import ModalTitle from '../modal-title';

const VacatedOvertunedModal = ({
  isOpen,
  onClose,
  submitAnotherJudj
}: TModalProps) => {
  return (
    <Modal isOpen={isOpen} onClose={onClose}>
      <>
        <ModalTitle title="Unfortunately, vacated, overturned, or discharged judgments are unenforceable." />
        <ModalActions
          actions={[
            <Button
              label="Submit Another Judgment"
              variant="primary"
              onClick={submitAnotherJudj}
            />,
            <Button
              label="Go to the main page"
              variant="secondary"
              onClick={() => {
                redirectToMainPage();
              }}
            />,
            <Button label="Back" variant="secondary" onClick={onClose} />
          ]}
        />
      </>
    </Modal>
  );
};

export default VacatedOvertunedModal;
