import { useEffect, useState } from 'react';
import TextField from 'components/shared/text-field/text-field';
import { TDebtorsProps } from '.';
import { getErrorMsg, getFieldStatus } from 'utils/form';

const DebtorsAdditionalInformationForm = ({
  handleFormAnswers,
  userFormsAnswers,
  errors,
  debtorId
}: TDebtorsProps) => {
  const fieldName = `${debtorId}debtorAddInformation`;
  const fieldState = getFieldStatus(userFormsAnswers, fieldName);
  const [fieldValue, setValue] = useState(fieldState?.value || '');

  // const placeholder: Record<string, string> = {
  //   debtorName: 'Enter debtor name',
  //   coDeptor0Name: 'Enter co-debtor name',
  //   coDeptor1Name: 'Enter co-debtor 2 name',
  //   coDeptor2Name: 'Enter co-debtor 3 name',
  //   coDeptor3Name: 'Enter co-debtor 4 name'
  // };

  useEffect(() => {
    handleFormAnswers(fieldName, fieldValue);
  }, []);

  return (
    <TextField
      placeholder="Ex: associations, family, spouse, etc."
      value={fieldValue}
      errorMsg={getErrorMsg(errors, fieldName)}
      onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
        const value = event.target.value;
        setValue(value);
        handleFormAnswers(fieldName, value);
      }}
    />
  );
};

export default DebtorsAdditionalInformationForm;
