/* eslint-disable @typescript-eslint/no-explicit-any */
import { useEffect, useState, useMemo } from 'react';
import { TQuestionFormProps } from '.';
import { getErrorMsg, getFieldStatus } from 'utils/form';
import {
  Select,
  SelectChangeEvent,
  MenuItem,
  FormControl,
  InputLabel,
  FormHelperText
} from '@mui/material';
import { courts } from 'constants/form';
import Placeholder from 'components/shared/placeholder/placeholder';

const FilingCourtForm = ({
  handleFormAnswers,
  errors,
  userFormsAnswers
}: TQuestionFormProps) => {
  const filingCourtFieldState = getFieldStatus(userFormsAnswers, 'filingCourt');
  const fillingCountyFieldState = getFieldStatus(
    userFormsAnswers,
    'filingCounty'
  );

  const [courtOptions, setCourtOptions] = useState<string[] | null>([]);
  const [fieldValue, setValue] = useState<string | undefined>(
    filingCourtFieldState?.value || ''
  );

  useMemo(() => {
    const currentCourts = (courts as Record<string, string[]>)[
      fillingCountyFieldState?.value || ''
    ];
    const isRightCourts = currentCourts?.some((val) => val === fieldValue);
    if (!isRightCourts) {
      setValue('');
    } else {
      setValue(filingCourtFieldState?.value || '');
    }
    setCourtOptions(currentCourts || null);
  }, [fillingCountyFieldState?.value]);

  useEffect(() => {
    handleFormAnswers('filingCourt', fieldValue);
  }, [fieldValue]);

  const handleChangeCourt = (event: SelectChangeEvent) => {
    const value = event.target.value as string;
    setValue(value);
  };

  return (
    <>
      <FormControl error={!!getErrorMsg(errors, 'filingCourt')} fullWidth>
        <InputLabel id="court" shrink>
          Filing Court
        </InputLabel>
        <Select
          fullWidth
          value={fieldValue}
          onChange={handleChangeCourt}
          label="Filing Court"
          labelId="court"
          disabled={!courtOptions}
          notched
          displayEmpty
          renderValue={(selected) =>
            !selected.length ? <Placeholder text="Filing Court" /> : selected
          }
        >
          {(courtOptions || []).map((court) => {
            return (
              <MenuItem value={court} key={court}>
                {court}
              </MenuItem>
            );
          })}
        </Select>
        {!!getErrorMsg(errors, 'filingCourt') && (
          <FormHelperText>{getErrorMsg(errors, 'filingCourt')}</FormHelperText>
        )}
      </FormControl>
    </>
  );
};

export default FilingCourtForm;
