/* eslint-disable @typescript-eslint/no-explicit-any */
import { useEffect, useState } from 'react';
import { TQuestionFormProps } from '.';
import { getErrorMsg, getFieldStatus } from 'utils/form';
import {
  Select,
  SelectChangeEvent,
  MenuItem,
  FormControl,
  InputLabel,
  FormHelperText
} from '@mui/material';
import { fillingCounty } from '../../constants/form';
import Placeholder from 'components/shared/placeholder/placeholder';

const FilingCountyForm = ({
  handleFormAnswers,
  errors,
  userFormsAnswers
}: TQuestionFormProps) => {
  const fieldState = getFieldStatus(userFormsAnswers, 'filingCounty');
  const [fieldValue, setValue] = useState(fieldState?.value || '');

  useEffect(() => {
    handleFormAnswers('filingCounty', fieldValue);
  }, [fieldValue]);

  const handleChangeCounty = (event: SelectChangeEvent) => {
    const value = event.target.value as string;
    setValue(value);
  };

  return (
    <>
      <FormControl
        variant="outlined"
        error={!!getErrorMsg(errors, 'filingCounty')}
        fullWidth
      >
        <InputLabel id="county" shrink>
          Filing County
        </InputLabel>
        <Select
          fullWidth
          value={fieldValue}
          onChange={handleChangeCounty}
          label="Filing County"
          labelId="county"
          notched
          displayEmpty
          renderValue={(selected) =>
            !selected.length ? <Placeholder text="Filing County" /> : selected
          }
        >
          {fillingCounty.map((county) => {
            return (
              <MenuItem value={county} key={county}>
                {county}
              </MenuItem>
            );
          })}
        </Select>
        {!!getErrorMsg(errors, 'filingCounty') && (
          <FormHelperText>{getErrorMsg(errors, 'filingCounty')}</FormHelperText>
        )}
      </FormControl>
    </>
  );
};

export default FilingCountyForm;
