import { Button, ButtonProps } from '@mui/material';
import { ReactComponent as EditIcon } from '../../icons/tabler_edit.svg';
import styles from './question-review-title.module.scss';
import { styled } from '@mui/material/styles';
import { isMobile } from 'react-device-detect';

type TReviewTitleProps = {
  title: string;
  onEdit: () => void;
};

const EditBtn = styled(Button)<ButtonProps>(() => ({
  backgroundColor: 'transparent',
  fontWeight: 500,
  padding: '9px',
  marginLeft: 15,
  textTransform: 'capitalize',
  '&:hover': {
    opacity: 0.85,
    backgroundColor: 'transparent'
  }
}));

const QuestionReviewTitle = ({ title, onEdit }: TReviewTitleProps) => {
  return (
    <div className={styles.reviewAnswerTitleWrap}>
      <h6 className={styles.reviewAnwerTitle}>{title}</h6>
      <EditBtn startIcon={<EditIcon />} onClick={onEdit}>
        {isMobile ? '' : 'Edit'}
      </EditBtn>
    </div>
  );
};

export default QuestionReviewTitle;
