import { useEffect, useState } from 'react';
import { TQuestionFormProps } from '.';
import { getErrorMsg, getFieldStatus, maskPhoneNumber } from 'utils/form';
import { TextField } from '@mui/material';

const DebtorsPhoneForm = ({
  handleFormAnswers,
  userFormsAnswers,
  errors,
  currentQuestion
}: TQuestionFormProps) => {
  const debtorNumber = currentQuestion?.debtorNumber || '';
  const mobPhoneFieldName = `debtorPhoneMob${debtorNumber}`;
  const altPhoneFieldName = `debtorPhoneAlt${debtorNumber}`;

  const mobPhoneFieldState = getFieldStatus(
    userFormsAnswers,
    mobPhoneFieldName
  );
  const altPhoneFieldState = getFieldStatus(
    userFormsAnswers,
    altPhoneFieldName
  );
  const [mobPhoneFieldValue, setMobPhoneValue] = useState(
    mobPhoneFieldState?.value || ''
  );
  const [altPhoneFieldValue, setAltPhoneValue] = useState(
    altPhoneFieldState?.value || undefined
  );

  useEffect(() => {
    handleFormAnswers(mobPhoneFieldName, mobPhoneFieldValue);
    handleFormAnswers(altPhoneFieldName, altPhoneFieldValue);
  }, []);

  return (
    <>
      <div className="field">
        <TextField
          label="Mobile Phone"
          placeholder={`Enter debtor’s mobile number`}
          value={mobPhoneFieldValue}
          error={!!getErrorMsg(errors, mobPhoneFieldName)}
          helperText={getErrorMsg(errors, mobPhoneFieldName)}
          inputProps={{
            maxLength: 14
          }}
          InputLabelProps={{ shrink: true }}
          onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
            // eslint-disable-next-line no-useless-escape
            const value = event.target.value.replace(/[\)\( -]/gm, '');
            const maskedPhoneNumber = maskPhoneNumber(value);
            setMobPhoneValue(maskedPhoneNumber);
            handleFormAnswers(mobPhoneFieldName, maskedPhoneNumber);
          }}
        />
      </div>
      <div className="field">
        <TextField
          label="Alt. Phone"
          placeholder={`Enter debtor’s alt. phone number`}
          value={altPhoneFieldValue}
          error={!!getErrorMsg(errors, altPhoneFieldName)}
          helperText={getErrorMsg(errors, altPhoneFieldName)}
          inputProps={{
            maxLength: 14
          }}
          InputLabelProps={{ shrink: true }}
          onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
            // eslint-disable-next-line no-useless-escape
            const value = event.target.value.replace(/[\)\( -]/gm, '');
            const maskedPhoneNumber = maskPhoneNumber(value);
            setAltPhoneValue(maskedPhoneNumber);
            handleFormAnswers(altPhoneFieldName, maskedPhoneNumber);
          }}
        />
      </div>
    </>
  );
};

export default DebtorsPhoneForm;
