export type TQuestion = {
  label: string;
  title: string;
  subTitle?: string;
  answers?: Array<TAnswerValues>;
  withNextBtn?: boolean;
  formId?: TFormId;
  progressLabel: string;
  modalId?: TModalId;
  deptorId?: number;
  helperText?: string;
  isDisableForBusiness?: boolean;
  isBusinessDebtor?: boolean;
  debtorNumber?: TNumberOfDebtors;
  subtitleTooltip?: string;
  titleTooltip?: string;
  isReviewPage?: boolean;
};

export type TAnswerValues = {
  value: string;
  answerId?: number | string;
};

export type TQuestionWithId = TQuestion & { id: number };

export type TQuestionsList = Array<TQuestionWithId>;

export type TNumberOfDebtors = 0 | 1 | 2 | 3 | 4;

