export const QUESTIONS_LIST: TQuestionsList = QUESTIONS.map(
  (question, ind) => ({ ...question, id: ind + 1 })
);

export const generateNewQuestions = (
  debtors: string[],
  isBussinessCreditorType: boolean
): TQuestionWithId[] => {
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  let additionalQuestions: any[] = [];
  debtors.forEach((debtor, ind) => {
    additionalQuestions = [
      ...additionalQuestions,
      ...generateDebtorQuestions(ind as TNumberOfDebtors, debtor)
    ];
  });

  const qiestions = isBussinessCreditorType
    ? QUESTIONS_LIST.filter((question) => !question.isDisableForBusiness)
    : QUESTIONS_LIST;

  const threeLastQuestions = qiestions.slice(qiestions.length - 3);
  const mergedQuestion = [
    ...qiestions.slice(0, qiestions.length - 3),
    ...additionalQuestions,
    ...threeLastQuestions
  ];
  const newQuestions = mergedQuestion.map((el, ind) => ({
    ...el,
    id: ind + 1
  }));

  return newQuestions;
};
