import { Box, Button, Grid, ButtonProps } from '@mui/material';
import Title from 'components/shared/title/title';
import { BOX_FIELD_MB } from 'constants/spacing';
import { TQuestionFormProps } from '.';
import DebtorsAddressForm from './debtor-address';
import DebtorsBirthdayForm from './debtor-birthday';
import DebtorsEmailForm from './debtor-email';
import DebtorsEmploymentInformationForm from './debtor-employment-info';
import DebtorsNameForm from './debtor-name';
import DebtorsSocSecForm from './debtor-soc-sec-num';
import DebtorsBusinessNameForm from './debtor-bis-name';
import DebtorsAdditionalInformationForm from './debtor-additional-info';
import formStyles from './forms.module.scss';
import MobilePhoneForm from './mobile-phone-form';
import DebtorsFinancialInformationForm from './debtor-fin-info';
import { ReactComponent as BusinessIcon } from '../../icons/business.svg';
import { ReactComponent as IndividualIcon } from '../../icons/individual.svg';
import { ReactComponent as DeleteIcon } from '../../icons/delete.svg';
import { styled } from '@mui/material/styles';
import Subtitle from 'components/shared/subtitle/subtitle';

const DebtorButton = styled(Button)<ButtonProps>(() => ({
  backgroundColor: '#F4F6FE',
  fontWeight: 600,
  minWidth: '270px',
  padding: '9px 27px',
  '&:hover': {
    opacity: 0.85
  }
}));

const DeleteDebtorBtn = styled(Button)<ButtonProps>(() => ({
  backgroundColor: 'transparent',
  color: '#D64A41',
  fontWeight: 500,
  padding: '9px',
  textTransform: 'capitalize',
  '&:hover': {
    opacity: 0.85,
    backgroundColor: 'transparent'
  }
}));

const DebtorsInfoForm = ({
  handleFormAnswers,
  userFormsAnswers,
  errors,
  currentQuestion,
  debtors,
  setDebtors
}: TQuestionFormProps) => {
  const addDebtor = (debtorType: 'individual' | 'business') => {
    if (setDebtors) {
      setDebtors((prevState) => [...prevState, debtorType]);
    }
  };

  const footerBtns = debtors && debtors.length < 5 && (
    <Grid container spacing="12px" justifyContent="flex-end" mt="2px">
      <Grid
        textAlign={{
          xs: 'center',
          md: 'right'
        }}
        item
        xs={12}
      >
        <DebtorButton
          startIcon={<IndividualIcon />}
          onClick={() => addDebtor('individual')}
        >
          Add Individual Debtor
        </DebtorButton>
      </Grid>
      <Grid
        textAlign={{
          xs: 'center',
          md: 'right'
        }}
        item
        xs={12}
      >
        <DebtorButton
          startIcon={<BusinessIcon />}
          onClick={() => addDebtor('business')}
        >
          Add Business Debtor
        </DebtorButton>
      </Grid>
    </Grid>
  );

  return (
    <>
      {debtors?.map((debtor, ind) => {
        const isBusinessDebtor = debtor === 'business';
        const debtorId = `${debtor}${ind}`;
        const isMoreThanOne = debtors.length > 1;
        const isFirstDebtor = ind === 0;

        return (
          <div key={debtorId}>
            <div className={formStyles.titleFieldWithLabel}>
              <Box pt="26px" display="flex" justifyContent="space-between">
                <Title
                  text={`${!isFirstDebtor ? 'Co-' : ''}Debtor Information`}
                />
                {isMoreThanOne && (
                  <DeleteDebtorBtn
                    startIcon={<DeleteIcon />}
                    onClick={() => {
                      if (setDebtors) {
                        const newDebtors: string[] = [
                          ...debtors.slice(0, ind),
                          ...debtors.slice(ind + 1)
                        ];
                        const debtorsInfo = userFormsAnswers?.filter((anw) =>
                          anw.fieldName.includes(debtorId)
                        );
                        // delete info from user responses
                        debtorsInfo?.forEach(({ fieldName }) => {
                          handleFormAnswers(fieldName, '');
                        });
                        setDebtors(newDebtors);
                      }
                    }}
                  >
                    Delete
                  </DeleteDebtorBtn>
                )}
              </Box>
              <Subtitle
                className={formStyles.subtitle}
                variant="large"
                text="Provide responses for the following fields to the best of your knowledge. This information will assist us in expediting the recovery process."
              />
            </div>
            <Box marginBottom={BOX_FIELD_MB}>
              <Box
                className={
                  isBusinessDebtor
                    ? formStyles.titleField
                    : formStyles.titleFieldWithLabel
                }
              >
                <Title
                  variant="medium"
                  text={
                    isBusinessDebtor ? 'Business Debtor Name' : 'Debtor Name'
                  }
                  tooltipText={
                    isBusinessDebtor
                      ? 'If you received a letter from Creditor Solutions, you will find the business debtor name in the top right-hand corner of your notice.'
                      : 'If you received a letter from Creditor Solutions, you will find the debtor full name in the top right-hand corner of your notice.'
                  }
                />
              </Box>
              {isBusinessDebtor ? (
                <DebtorsBusinessNameForm
                  errors={errors}
                  handleFormAnswers={handleFormAnswers}
                  userFormsAnswers={userFormsAnswers}
                  currentQuestion={currentQuestion}
                  debtorId={debtorId}
                />
              ) : (
                <DebtorsNameForm
                  errors={errors}
                  handleFormAnswers={handleFormAnswers}
                  userFormsAnswers={userFormsAnswers}
                  currentQuestion={currentQuestion}
                  debtorId={debtorId}
                />
              )}
            </Box>
            <Box marginBottom={BOX_FIELD_MB}>
              <Box className={formStyles.titleField}>
                <Title
                  variant="medium"
                  text={`${isBusinessDebtor ? 'Business' : ''} Debtor Email`}
                />
              </Box>
              <DebtorsEmailForm
                errors={errors}
                handleFormAnswers={handleFormAnswers}
                userFormsAnswers={userFormsAnswers}
                currentQuestion={currentQuestion}
                debtorId={debtorId}
              />
            </Box>
            <Box marginBottom={BOX_FIELD_MB}>
              <Box className={formStyles.titleField}>
                <Title
                  variant="medium"
                  text={`${isBusinessDebtor ? 'Business' : ''} Debtor Address`}
                />
              </Box>
              <DebtorsAddressForm
                errors={errors}
                handleFormAnswers={handleFormAnswers}
                userFormsAnswers={userFormsAnswers}
                currentQuestion={currentQuestion}
                debtorId={debtorId}
              />
            </Box>
            <Box marginBottom={BOX_FIELD_MB}>
              <Box className={formStyles.titleField}>
                <Title
                  variant="medium"
                  text={`${isBusinessDebtor ? 'Business' : ''} Debtor Phone`}
                />
              </Box>
              <MobilePhoneForm
                errors={errors}
                handleFormAnswers={handleFormAnswers}
                userFormsAnswers={userFormsAnswers}
                currentQuestion={currentQuestion}
                placeholder={`Enter${
                  isBusinessDebtor ? ' business' : ''
                } debtor phone`}
                fieldName={`${debtorId}debtorPhoneMob`}
              />
            </Box>
            <Box marginBottom={BOX_FIELD_MB}>
              <Box className={formStyles.titleField}>
                <Title
                  variant="medium"
                  text={`${
                    isBusinessDebtor ? 'Business' : ''
                  } Debtor Alt. Phone`}
                />
              </Box>
              <MobilePhoneForm
                errors={errors}
                handleFormAnswers={handleFormAnswers}
                userFormsAnswers={userFormsAnswers}
                currentQuestion={currentQuestion}
                placeholder={`Enter${
                  isBusinessDebtor ? ' business' : ''
                } debtor alt. phone`}
                fieldName={`${debtorId}debtorPhoneAlt`}
              />
            </Box>
            <Box marginBottom={BOX_FIELD_MB}>
              <Box className={formStyles.titleField}>
                <Title
                  variant="medium"
                  text={isBusinessDebtor ? 'Business Debtor EIN' : 'Debtor SSN'}
                />
              </Box>
              <DebtorsSocSecForm
                errors={errors}
                handleFormAnswers={handleFormAnswers}
                userFormsAnswers={userFormsAnswers}
                currentQuestion={currentQuestion}
                debtorId={debtorId}
                isBusinessDebtor={isBusinessDebtor}
              />
            </Box>
            {!isBusinessDebtor && (
              <Box marginBottom={BOX_FIELD_MB}>
                <Box className={formStyles.titleField}>
                  <Title variant="medium" text="Debtor Age" />
                </Box>
                <DebtorsBirthdayForm
                  errors={errors}
                  handleFormAnswers={handleFormAnswers}
                  userFormsAnswers={userFormsAnswers}
                  currentQuestion={currentQuestion}
                  debtorId={debtorId}
                />
              </Box>
            )}
            {!isBusinessDebtor && (
              <Box marginBottom={BOX_FIELD_MB}>
                <Box className={formStyles.titleField}>
                  <Title
                    variant="medium"
                    text={`${
                      isBusinessDebtor ? 'Business' : ''
                    } Debtor Employment Information`}
                  />
                </Box>
                <DebtorsEmploymentInformationForm
                  errors={errors}
                  handleFormAnswers={handleFormAnswers}
                  userFormsAnswers={userFormsAnswers}
                  currentQuestion={currentQuestion}
                  debtorId={debtorId}
                />
              </Box>
            )}
            <Box marginBottom={BOX_FIELD_MB}>
              <Box className={formStyles.titleFieldWithLabel}>
                <Title
                  variant="medium"
                  text={`${
                    isBusinessDebtor ? 'Business' : ''
                  } Debtor Financial Information`}
                  tooltipText={
                    isBusinessDebtor
                      ? 'Set forth any known institutions where business debtor maintains financial accounts, including, but not limited to: checking accounts, savings accounts, mortgage accounts, brokerage accounts, cryptocurrency accounts, or online money transfer accounts.'
                      : 'Set forth any known institutions where debtor maintains financial accounts, including, but not limited to: checking accounts, savings accounts, mortgage accounts, brokerage accounts, cryptocurrency accounts, or online money transfer accounts.'
                  }
                />
              </Box>
              <DebtorsFinancialInformationForm
                errors={errors}
                handleFormAnswers={handleFormAnswers}
                userFormsAnswers={userFormsAnswers}
                currentQuestion={currentQuestion}
                debtorId={debtorId}
              />
            </Box>
            <Box marginBottom={BOX_FIELD_MB}>
              <Box className={formStyles.titleFieldWithLabel}>
                <Title
                  variant="medium"
                  text={`${
                    isBusinessDebtor ? 'Business' : ''
                  } Debtor Additional Information`}
                />
              </Box>
              <DebtorsAdditionalInformationForm
                errors={errors}
                handleFormAnswers={handleFormAnswers}
                userFormsAnswers={userFormsAnswers}
                currentQuestion={currentQuestion}
                debtorId={debtorId}
              />
            </Box>
          </div>
        );
      })}
      {footerBtns}
    </>
  );
};

export default DebtorsInfoForm;
