import styles from './question-review-response.module.scss';

type TQuestionReviewResponseProps = {
  label?: string;
  value?: string;
};

const QuestionReviewResponse = ({
  label,
  value
}: TQuestionReviewResponseProps) => {
  return (
    <p className={styles.fieldResponse}>
      <span className={value ? '' : styles.fieldResponseValue}>{label}</span>
      {value ? (
        <>
          <span>
            {label && ': '}
            <span className={styles.fieldResponseValue}>{value}</span>
          </span>
        </>
      ) : (
        ''
      )}
    </p>
  );
};

export default QuestionReviewResponse;
