import { useEffect, useState } from 'react';
//import TextField from 'components/shared/text-field/text-field';
import { TDebtorsProps } from '.';
import { getErrorMsg, getFieldStatus } from 'utils/form';
import { Grid, TextField } from '@mui/material';

const DebtorsFinancialInformationForm = ({
  handleFormAnswers,
  userFormsAnswers,
  errors,
  debtorId
}: TDebtorsProps) => {
  const finSourceNameFieldName = `${debtorId}debtorSourceName`;
  const accountNumberFieldName = `${debtorId}debtorAccountNumber`;
  const accountTypeFieldName = `${debtorId}debtorAccountType`;

  const finSourceNamefieldState = getFieldStatus(
    userFormsAnswers,
    finSourceNameFieldName
  );
  const accountNumberFieldState = getFieldStatus(
    userFormsAnswers,
    accountNumberFieldName
  );
  const accountTypeFieldState = getFieldStatus(
    userFormsAnswers,
    accountTypeFieldName
  );

  const [financialSourceNameValue, setFinancialSourceNameValue] = useState(
    finSourceNamefieldState?.value || ''
  );
  const [accountNumberValue, setAccountNumberValue] = useState(
    accountNumberFieldState?.value || ''
  );
  const [accountTypeValue, setAccountTypeValue] = useState(
    accountTypeFieldState?.value || ''
  );

  useEffect(() => {
    handleFormAnswers(finSourceNameFieldName, financialSourceNameValue);
    handleFormAnswers(accountNumberFieldName, accountNumberValue);
    handleFormAnswers(accountTypeFieldName, accountTypeValue);
  }, [financialSourceNameValue, accountNumberValue, accountTypeValue]);

  return (
    <>
      <Grid container spacing={2}>
        <Grid item xs={12} md={4.5}>
          <TextField
            label="Financial Source Name"
            placeholder="Enter financial source name"
            value={financialSourceNameValue}
            error={!!getErrorMsg(errors, financialSourceNameValue)}
            helperText={getErrorMsg(errors, financialSourceNameValue)}
            onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
              const value = event.target.value;
              setFinancialSourceNameValue(value);
            }}
            InputLabelProps={{ shrink: true }}
          />
        </Grid>
        <Grid item xs={12} md={3}>
          <TextField
            label="Account Number"
            placeholder="Enter account number"
            value={accountNumberValue}
            error={!!getErrorMsg(errors, accountNumberValue)}
            helperText={getErrorMsg(errors, accountNumberValue)}
            onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
              const value = event.target.value;
              setAccountNumberValue(value);
            }}
            InputLabelProps={{ shrink: true }}
          />
        </Grid>
        <Grid item xs={12} md={4.5}>
          <TextField
            label="Account Type"
            placeholder="Enter account type"
            value={accountTypeValue}
            error={!!getErrorMsg(errors, accountTypeValue)}
            helperText={getErrorMsg(errors, accountTypeValue)}
            onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
              const value = event.target.value;
              setAccountTypeValue(value);
            }}
            InputLabelProps={{ shrink: true }}
          />
        </Grid>
      </Grid>
    </>
  );
};

export default DebtorsFinancialInformationForm;
