/* eslint-disable @typescript-eslint/no-explicit-any */
import { useState, useEffect } from 'react';
import SignatureCanvas from 'react-signature-canvas';
//import TextField from 'components/shared/text-field/text-field';
import { TQuestionFormProps } from '.';
import { getErrorMsg, getFieldStatus } from 'utils/form';
import textFieldStyles from '../shared/text-field/text-field.module.scss';
import styles from './forms.module.scss';
import { Button, Box, TextField } from '@mui/material';
import ClientServiceAgreementModal from 'components/modals/client-service-agreement/client-service-agreement';
import { isMobile } from 'react-device-detect';

export type TClientServiceAgreement = TQuestionFormProps & {
  setEsign: React.Dispatch<unknown>;
  esign: { trimmedDataURL: string; dataURL: string };
};

const ClientServiceAgreement = ({
  handleFormAnswers,
  userFormsAnswers,
  errors,
  esign,
  setEsign
}: TClientServiceAgreement) => {
  const fnameFieldState = getFieldStatus(userFormsAnswers, 'firstName');
  const lnameFieldState = getFieldStatus(userFormsAnswers, 'lastName');
  const submissionMessageState = getFieldStatus(
    userFormsAnswers,
    'submissionMessage'
  );

  const [fnameFieldValue, setFNameFieldValue] = useState(
    fnameFieldState?.value || ''
  );
  const [lnameFieldValue, setLNameFieldValue] = useState(
    lnameFieldState?.value || ''
  );
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [submissionMessageFieldValue, setSubmissionMessageField] = useState(
    submissionMessageState?.value || ''
  );
  const [openModal, setOpenModal] = useState(false);

  let signPad: any = {};
  const esignError = getErrorMsg(errors, 'eSign');

  const handleSaveEsign = () => {
    const dataURL = signPad.getCanvas().toDataURL('image/png');
    handleFormAnswers('eSign', dataURL);

    setEsign({
      trimmedDataURL: signPad.getTrimmedCanvas().toDataURL('image/png'),
      dataURL
    });
  };

  const handleOnClear = () => {
    signPad.clear();
    handleFormAnswers('eSign', '');
    setEsign({
      trimmedDataURL: null,
      dataURL: null
    });
  };

  useEffect(() => {
    handleFormAnswers('firstName', fnameFieldValue);
    handleFormAnswers('lastName', lnameFieldValue);
    handleFormAnswers('submissionMessage', submissionMessageFieldValue);

    if (signPad) {
      signPad.fromDataURL(esign?.dataURL);
    }
  }, [fnameFieldValue, lnameFieldValue, submissionMessageFieldValue]);

  return (
    <>
      {openModal && (
        <ClientServiceAgreementModal
          isOpen={openModal}
          onClose={() => {
            setOpenModal(false);
          }}
        />
      )}
      <Box
        display="flex"
        justifyContent="flex-end"
        style={{
          margin: '0px 0 10px'
        }}
      >
        <Button onClick={() => setOpenModal(true)}>Show all</Button>
      </Box>
      <div
        className={`${styles.signWrap} ${
          esignError ? styles.signWrapError : ''
        }`}
      >
        <p className={styles.signTitle}>Signature*</p>
        <SignatureCanvas
          canvasProps={{ className: styles.signPad }}
          clearOnResize={false}
          ref={(ref: any) => {
            signPad = ref;
          }}
          onEnd={handleSaveEsign}
        />
      </div>
      {esignError && (
        <p className={textFieldStyles.errorMsg} style={{ marginBottom: 4 }}>
          {esignError}
        </p>
      )}
      <div className={styles.esignFooter}>
        {isMobile ? (
          <p className={styles.signHint}>
            To sign, swipe your finger across the screen.
          </p>
        ) : (
          <p className={styles.signHint}>
            Use your mouse, finger, trackpad, or stylus to sign.
          </p>
        )}
        <button className={styles.clearBtn} onClick={handleOnClear}>
          Clear
        </button>
      </div>
      <div className="fieldsRow namesRow">
        <div className="fieldsCol namesCol">
          <div className="field">
            <TextField
              label="First Name*"
              placeholder="George"
              value={fnameFieldValue}
              error={!!getErrorMsg(errors, 'firstName')}
              helperText={getErrorMsg(errors, 'firstName')}
              onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                const value = event.target.value;
                setFNameFieldValue(value);
              }}
              InputLabelProps={{ shrink: true }}
            />
          </div>
        </div>
        <div className="fieldsCol namesCol">
          <div className="field">
            <TextField
              label="Last Name*"
              placeholder="McFly"
              value={lnameFieldValue}
              error={!!getErrorMsg(errors, 'lastName')}
              helperText={getErrorMsg(errors, 'lastName')}
              onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                const value = event.target.value;
                setLNameFieldValue(value);
              }}
              InputLabelProps={{ shrink: true }}
            />
          </div>
        </div>
      </div>
      {/* <div className="dateRow">
        <div className="field">
          <TextField
            label="Submission Message"
            placeholder="Enter submission message"
            value={submissionMessageFieldValue}
            error={!!getErrorMsg(errors, 'submissionMessage')}
            helperText={getErrorMsg(errors, 'submissionMessage')}
            onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
              const value = event.target.value;
              setSubmissionMessageField(value);
              handleFormAnswers(
                'submissionMessage',
                submissionMessageFieldValue
              );
            }}
            InputLabelProps={{ shrink: true }}
          />
        </div>
      </div> */}
    </>
  );
};

export default ClientServiceAgreement;
