import { useState, useEffect } from 'react';
import { TQuestionFormProps } from '.';
import { getErrorMsg, getFieldStatus, itemsToString } from 'utils/form';
import Title from 'components/shared/title/title';
import { Box, Grid, Button, TextField } from '@mui/material';
import formStyles from './forms.module.scss';
import EmailForm from './email-form';
import CreditorAddressForm from './creditor-address-form';
import MobilePhoneForm from './mobile-phone-form';
import BusinessTitle from './business-title-form';
import { BOX_FIELD_MB } from 'constants/spacing';

const CreditorInfoForm = ({
  handleFormAnswers,
  userFormsAnswers,
  isBusinessCreditorType,
  errors
}: TQuestionFormProps) => {
  const creditorNameFieldState = getFieldStatus(
    userFormsAnswers,
    'creditorName'
  );
  const creditorLastNameFieldState = getFieldStatus(
    userFormsAnswers,
    'creditorLastName'
  );
  const creditorMidInitialFieldState = getFieldStatus(
    userFormsAnswers,
    'creditorMiddleInitial'
  );
  const creditorSuffixFieldState = getFieldStatus(
    userFormsAnswers,
    'creditorSuffix'
  );

  const creditorContactNameFieldState = getFieldStatus(
    userFormsAnswers,
    'creditorContactName'
  );
  const creditorContactLastNameFieldState = getFieldStatus(
    userFormsAnswers,
    'creditorContactLastName'
  );
  const creditorContactMidInitialFieldState = getFieldStatus(
    userFormsAnswers,
    'creditorContactMiddleInitial'
  );
  const creditorContactSuffixFieldState = getFieldStatus(
    userFormsAnswers,
    'creditorContactSuffix'
  );

  const businessCreditorNameFieldState = getFieldStatus(
    userFormsAnswers,
    'businessCreditorName'
  );

  const [creditorName, setCreditorNameValue] = useState(
    creditorNameFieldState?.value || ''
  );
  const [creditorLastName, setCreditorLastNameValue] = useState(
    creditorLastNameFieldState?.value || ''
  );
  const [creditorMidInitial, setCreditorMidInitialValue] = useState(
    creditorMidInitialFieldState?.value || ''
  );
  const [creditorSuffix, setCreditorSuffixValue] = useState(
    creditorSuffixFieldState?.value || ''
  );

  const [creditorContactName, setCreditorContactNameValue] = useState(
    creditorContactNameFieldState?.value || ''
  );
  const [creditorContactLastName, setCreditorContactLastNameValue] = useState(
    creditorContactLastNameFieldState?.value || ''
  );
  const [creditorContactMidInitial, setCreditorContactMidInitialValue] =
    useState(creditorContactMidInitialFieldState?.value || '');
  const [creditorContactSuffix, setCreditorContactSuffixValue] = useState(
    creditorContactSuffixFieldState?.value || ''
  );

  const [businessCreditorName, setBusinessCreditorNameValue] = useState(
    businessCreditorNameFieldState?.value || ''
  );

  const setAnswer = (fieldName: string, value: string) => {
    handleFormAnswers(fieldName, value);
    switch (fieldName) {
      case 'creditorName':
        setCreditorNameValue(value);
        break;
      case 'creditorMiddleInitial':
        setCreditorMidInitialValue(value);
        break;
      case 'creditorLastName':
        setCreditorLastNameValue(value);
        break;
      case 'creditorSuffix':
        setCreditorSuffixValue(value);
        break;
      case 'creditorContactName':
        setCreditorContactNameValue(value);
        break;
      case 'creditorContactMiddleInitial':
        setCreditorContactMidInitialValue(value);
        break;
      case 'creditorContactLastName':
        setCreditorContactLastNameValue(value);
        break;
      case 'creditorContactSuffix':
        setCreditorContactSuffixValue(value);
        break;
      case 'businessCreditorName':
        setBusinessCreditorNameValue(value);
        break;
      default:
        break;
    }
  };

  useEffect(() => {
    const creditorFullName = itemsToString([
      creditorName,
      creditorMidInitial,
      creditorLastName,
      creditorSuffix
    ]);
    handleFormAnswers('creditorFullName', creditorFullName);

    const creditorContactFullName = itemsToString([
      creditorContactName,
      creditorContactMidInitial,
      creditorContactLastName,
      creditorContactSuffix
    ]);
    handleFormAnswers(
      isBusinessCreditorType
        ? 'creditorContactBisFullName'
        : 'creditorContactFullName',
      creditorContactFullName
    );
  }, [
    creditorName,
    creditorMidInitial,
    creditorLastName,
    creditorSuffix,
    creditorContactName,
    creditorContactMidInitial,
    creditorContactLastName,
    creditorContactSuffix
  ]);

  const handleCopyName = () => {
    setAnswer('creditorContactName', creditorName);
    setAnswer('creditorContactMiddleInitial', creditorMidInitial);
    setAnswer('creditorContactLastName', creditorLastName);
    setAnswer('creditorContactSuffix', creditorSuffix);
  };

  return (
    <>
      <Box marginBottom={BOX_FIELD_MB}>
        {isBusinessCreditorType ? (
          <Box className={formStyles.titleField}>
            <Title
              text="Company Name"
              required
              tooltipText="If you received a letter from Creditor Solutions, you will find the company name in the top right-hand corner of your notice."
              variant="medium"
            />
          </Box>
        ) : (
          <Box className={formStyles.titleFieldWithLabel}>
            <Title
              text="Creditor Name"
              required
              variant="medium"
              tooltipText="If you received a letter from Creditor Solutions, you will find the creditor name in the top right-hand corner of your notice."
            />
          </Box>
        )}
        <Grid container spacing={2}>
          {isBusinessCreditorType ? (
            <Grid item xs={12}>
              <TextField
                placeholder="Enter company name"
                value={businessCreditorName}
                error={!!getErrorMsg(errors, 'businessCreditorName')}
                helperText={getErrorMsg(errors, 'businessCreditorName')}
                onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                  const value = event.target.value;
                  setAnswer('businessCreditorName', value);
                }}
                InputLabelProps={{ shrink: true }}
              />
            </Grid>
          ) : (
            <>
              <Grid item xs={12} md={4}>
                <TextField
                  placeholder="Emmett"
                  label="First Name*"
                  value={creditorName}
                  error={!!getErrorMsg(errors, 'creditorName')}
                  helperText={getErrorMsg(errors, 'creditorName')}
                  onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                    const value = event.target.value;
                    setAnswer('creditorName', value);
                  }}
                  InputLabelProps={{ shrink: true }}
                />
              </Grid>
              <Grid item xs={12} md={2}>
                <TextField
                  placeholder="Doc"
                  label="Middle Initial"
                  value={creditorMidInitial}
                  error={!!getErrorMsg(errors, 'creditorMiddleInitial')}
                  helperText={getErrorMsg(errors, 'creditorMiddleInitial')}
                  onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                    const value = event.target.value;
                    setAnswer('creditorMiddleInitial', value);
                  }}
                  InputLabelProps={{ shrink: true }}
                />
              </Grid>
              <Grid item xs={12} md={4}>
                <TextField
                  placeholder="Brown"
                  label="Last Name*"
                  value={creditorLastName}
                  error={!!getErrorMsg(errors, 'creditorLastName')}
                  helperText={getErrorMsg(errors, 'creditorLastName')}
                  onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                    const value = event.target.value;
                    setAnswer('creditorLastName', value);
                  }}
                  InputLabelProps={{ shrink: true }}
                />
              </Grid>
              <Grid item xs={12} md={2}>
                <TextField
                  placeholder="Suffix"
                  label="Suffix"
                  value={creditorSuffix}
                  error={!!getErrorMsg(errors, 'creditorSuffix')}
                  helperText={getErrorMsg(errors, 'creditorSuffix')}
                  onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                    const value = event.target.value;
                    setAnswer('creditorSuffix', value);
                  }}
                  InputLabelProps={{ shrink: true }}
                />
              </Grid>
            </>
          )}
        </Grid>
      </Box>
      <Box marginBottom={BOX_FIELD_MB}>
        <Box
          display="flex"
          justifyContent="space-between"
          alignItems="center"
          className={formStyles.titleFieldWithLabel}
        >
          <Title
            variant="medium"
            text={`${
              isBusinessCreditorType ? 'Company Contact' : 'Creditor Contact'
            }`}
            required
          />
          {!isBusinessCreditorType && (
            <Box>
              <Button onClick={handleCopyName}>Same</Button>
            </Box>
          )}
        </Box>
        <Grid container spacing={2}>
          <Grid item xs={12} md={4}>
            <TextField
              placeholder="Marty"
              label="First Name*"
              value={creditorContactName}
              error={!!getErrorMsg(errors, 'creditorContactName')}
              helperText={getErrorMsg(errors, 'creditorContactName')}
              onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                const value = event.target.value;
                setAnswer('creditorContactName', value);
              }}
              InputLabelProps={{ shrink: true }}
            />
          </Grid>
          <Grid item xs={12} md={2}>
            <TextField
              placeholder="Middle initial"
              label="Middle Initial"
              value={creditorContactMidInitial}
              error={!!getErrorMsg(errors, 'creditorContactMiddleInitial')}
              helperText={getErrorMsg(errors, 'creditorContactMiddleInitial')}
              onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                const value = event.target.value;
                setAnswer('creditorContactMiddleInitial', value);
              }}
              InputLabelProps={{ shrink: true }}
            />
          </Grid>
          <Grid item xs={12} md={4}>
            <TextField
              placeholder="McFly"
              label="Last Name*"
              value={creditorContactLastName}
              error={!!getErrorMsg(errors, 'creditorContactLastName')}
              helperText={getErrorMsg(errors, 'creditorContactLastName')}
              onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                const value = event.target.value;
                setAnswer('creditorContactLastName', value);
              }}
              InputLabelProps={{ shrink: true }}
            />
          </Grid>
          <Grid item xs={12} md={2}>
            <TextField
              placeholder="Suffix"
              label="Suffix"
              value={creditorContactSuffix}
              error={!!getErrorMsg(errors, 'creditorContactSuffix')}
              helperText={getErrorMsg(errors, 'creditorContactSuffix')}
              onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                const value = event.target.value;
                setAnswer('creditorContactSuffix', value);
              }}
              InputLabelProps={{ shrink: true }}
            />
          </Grid>
        </Grid>
      </Box>
      {isBusinessCreditorType && (
        <Box marginBottom={BOX_FIELD_MB}>
          <Box className={formStyles.titleField}>
            <Title text="Contact Title" variant="medium" required />
          </Box>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <BusinessTitle
                errors={errors}
                handleFormAnswers={handleFormAnswers}
                userFormsAnswers={userFormsAnswers}
                isBusinessCreditorType={isBusinessCreditorType}
              />
            </Grid>
          </Grid>
        </Box>
      )}
      <Box marginBottom={BOX_FIELD_MB}>
        <Box className={formStyles.titleField}>
          <Title
            required
            variant="medium"
            text={isBusinessCreditorType ? 'Contact Email' : 'Creditor Email'}
          />
        </Box>
        <Grid container spacing={2}>
          <Grid item xs={12}>
            <EmailForm
              errors={errors}
              handleFormAnswers={handleFormAnswers}
              userFormsAnswers={userFormsAnswers}
              isBusinessCreditorType={isBusinessCreditorType}
              fieldName={isBusinessCreditorType ? 'bisEmail' : 'email'}
            />
          </Grid>
        </Grid>
      </Box>
      <Box marginBottom={BOX_FIELD_MB}>
        <Box className={formStyles.titleField}>
          <Title
            variant="medium"
            required
            text={
              isBusinessCreditorType ? 'Contact Address' : 'Creditor Address'
            }
            tooltipText={
              isBusinessCreditorType
                ? 'Enter the address where payments should be sent'
                : ''
            }
          />
        </Box>
        <Grid container spacing={2}>
          <Grid item xs={12}>
            <CreditorAddressForm
              errors={errors}
              handleFormAnswers={handleFormAnswers}
              userFormsAnswers={userFormsAnswers}
              isBusinessCreditorType={isBusinessCreditorType}
              fieldName={
                isBusinessCreditorType ? 'bisAddress' : 'creditorAddress'
              }
            />
          </Grid>
        </Grid>
      </Box>
      <Box marginBottom={BOX_FIELD_MB}>
        <Box className={formStyles.titleField}>
          <Title
            variant="medium"
            required
            text={isBusinessCreditorType ? 'Contact Phone' : 'Creditor Phone'}
          />
        </Box>
        <Grid container spacing={2}>
          <Grid item xs={12}>
            <MobilePhoneForm
              errors={errors}
              handleFormAnswers={handleFormAnswers}
              userFormsAnswers={userFormsAnswers}
              isBusinessCreditorType={isBusinessCreditorType}
              placeholder="Enter mobile phone"
              fieldName={isBusinessCreditorType ? 'bisPhone' : 'mobPhone'}
            />
          </Grid>
        </Grid>
      </Box>
      <Box marginBottom={{ xs: 0, md: BOX_FIELD_MB }}>
        <Box className={formStyles.titleField}>
          <Title
            variant="medium"
            text={
              isBusinessCreditorType
                ? 'Contact Alt. Phone'
                : 'Creditor Alt. Phone'
            }
          />
        </Box>
        <Grid container spacing={2}>
          <Grid item xs={12}>
            <MobilePhoneForm
              errors={errors}
              handleFormAnswers={handleFormAnswers}
              userFormsAnswers={userFormsAnswers}
              placeholder="Enter alt. phone"
              fieldName={isBusinessCreditorType ? 'bisAltPhone' : 'altPhone'}
              isBusinessCreditorType={isBusinessCreditorType}
            />
          </Grid>
        </Grid>
      </Box>
    </>
  );
};

export default CreditorInfoForm;
